import {
  Box,
  FormControl,
  FormControlLabel,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import { useState } from "react";
import {
  AcademicTestNamesEnum,
  ENglishTestNamesEnum,
} from "../../../../assets/enums";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import AcademicTestForm from "./academic";
import { setPopup } from "src/store/Slices/popupSlice";
import { Event } from "src/types/types";
import { editProfile } from "src/services";
import { setSharedInfo } from "src/store/Slices/sharedInfoSlice";
import EnglishTestForm from "./englishTestscores";

const TestScores = () => {
  const tests = useAppSelector((state) => state.testscores);
  const englishTestNames = Object.values(ENglishTestNamesEnum);
  const academicTestNames = Object.values(AcademicTestNamesEnum);
  const [selectedTest, setSelectedTest] = useState<string>("");
  const [selectedGRETest, setSelectedGRETest] = useState<string>("");
  const sharedInfo = useAppSelector((state) => state.sharedInfo);
  const dispatch = useAppDispatch();

  const academicTests = tests.data?.filter((test) =>
    academicTestNames.includes(test.name)
  );
  const englishTests = tests.data?.filter((test) =>
    englishTestNames.includes(test.name)
  );

  const [isSelected, setIsSelected] = useState<string>(() => {
    if (englishTests?.length > 0) {
      return "Yes";
    }
    return sharedInfo.data?.isPlanningToTakeLanguageTest ? "Planning to take" : "Not planning to take";
  });
  const [isGRESelected, setIsGRESelected] = useState<string>(() => {
    if (academicTests?.length > 0) {
      return "Yes";
    }
    return sharedInfo.data?.isPlanningToTakeAcademicTest ? "Planning to take" : "Not planning to take";
  });

  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "englishTest":
        setSelectedTest(event.data);
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "testscores",
                data: event.data,
                dimensions: {
                  width: "800px",
                  height: "57%",
                  // show: "none"
                },
              },
              type: "custom",
            },
          })
        );
        setSelectedTest("")
        break;
      case "academicTest":
        setSelectedGRETest(event.data)
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "testscores",
                data: event.data,
                dimensions: {
                  width: "800px",
                  height: "57%",

                },
              },
              type: "custom",
            },
          })
        );
        setSelectedGRETest("")
        break;
      case "englishTestSelection":
        setIsSelected(event.data);
        if (event.data === "Planning to take" || event.data === "Not planning to take") {
          try {
            const response = await editProfile({ isPlanningToTakeLanguageTest: Boolean(event.data === "Planning to take" ? "Yes" : "No") });
            dispatch(
              setSharedInfo({
                ...sharedInfo.data,
                isPlanningToTakeLanguageTest: response.data.data.isPlanningToTakeLanguageTest,
              })
            );
          } catch (error) {
            console.error("Error in API Call:", error);
          }
        }
        break;
        case "academicTestSelection":
        setIsGRESelected(event.data);
        if (event.data === "Planning to take" || event.data === "Not planning to take") {
          console.log(event.data === "Planning to take" ? "Yes" : "No")
          try {
            const response = await editProfile({ isPlanningToTakeAcademicTest: Boolean(event.data === "Planning to take" ? "Yes" : "No") });
            dispatch(
              setSharedInfo({
                ...sharedInfo.data,
                isPlanningToTakeAcademicTest: response.data.data.isPlanningToTakeAcademicTest,
              })
            );
          } catch (error) {
            console.error("Error in API Call:", error);
          }
        }
        break;
      default:
        break;
    }
  }

  const handleChange = () => { };

  return (
    <Stack spacing={2} sx={{
      px: 3, "&. MuiButtonBase-root-MuiRadio-root.Mui-checked": {
        color: "green"
      }
    }}>
      {isSelected === "No" ? (
        <Box>
          <InputLabel sx={{ color: "#000", fontWeight: 500 }}>
            English marks in class 12
          </InputLabel>
          <TextField
            type="number"
            onChange={handleChange}
            size="small"
          />
        </Box>
      ) : null}

      <Stack direction="column" spacing={2}>
        <Box sx={{ position: "relative", }}>
          <Box
            sx={{
              position: "absolute",
              top: 6,
              left: 8,
              right: 0,
              bottom: 0,
              zIndex: 0,
              borderRadius: "15px",
              background: `#D1D3F9`,
              width: "100%",
              height: "100%",
              transform: "translate(0px, -2px)"
            }}
          />
          <Box sx={{ position: "relative", background: "#F4F4FF", p: 1, borderRadius: "10px", display: "flex", justifyContent: "space-between", alignItems: "center" }}>
            <Box sx={{ mt: 1 }}>
              <Typography sx={{ position: "relative", width: "1rem", height: "1rem", borderRadius: "50%", background: "#FEA89B" }}>
                <Typography sx={{ position: "absolute", top: "-4px", left: "8px", width: "500px", color: "#000", fontWeight: 500 }}>
                  Have you taken any English Proficiency Test?
                </Typography>
              </Typography>
              <FormControl>
                <RadioGroup
                  row
                  name="englishTest"
                  value={isSelected}
                  // onChange={handleChange1}
                  onChange={(e: any) =>
                    eventHandler({
                      name: "englishTestSelection",
                      data: e.target.value,
                    })
                  }
                >
                  {["Yes",  "Planning to take", "Not planning to take"].map(
                    (option) => (
                      <FormControlLabel
                        key={option}
                        value={option}
                        control={<Radio />}
                        label={option}
                      />
                    )
                  )}
                </RadioGroup>
              </FormControl>
            </Box>

            {isSelected === "Yes" && englishTests?.length !== englishTestNames?.length && (
              <Box sx={{ borderRadius: "10px", p: 2, }}>
                <Select
                  displayEmpty
                  value={selectedTest}
                  size="small"
                  // onChange={handleTestSelection}
                  onChange={(e: any) =>
                    eventHandler({
                      name: "englishTest",
                      data: e.target.value,
                    })
                  }
                  sx={{ border: "1px solid #1C1F4066 !important", borderRadius: "25px", width: "150px" }}
                >
                  <MenuItem value="">Select a test</MenuItem>
                  {Object.entries(ENglishTestNamesEnum).map(([key, value]) => {
                    const isDisabled = englishTests?.some((test) => test.name === value);
                    return (
                      <MenuItem key={key} value={value} disabled={isDisabled}>
                        {key}
                      </MenuItem>
                    );
                  })}
                </Select>
              </Box>
            )}
          </Box>
        </Box>
        {isSelected === "Yes" ? <EnglishTestForm /> : null}
        <Box>

          <Box sx={{ position: "relative", }}>
            <Box
              sx={{
                position: "absolute",
                top: 6,
                left: 8,
                right: 0,
                bottom: 0,
                zIndex: 0,
                borderRadius: "15px",
                background: `#D1D3F9`,
                width: "100%",
                height: "100%",
                transform: "translate(0px, -2px)"
              }}
            />
            <Box sx={{ position: "relative", background: "#F4F4FF", p: 1, borderRadius: "10px", display: "flex", justifyContent: "space-between", alignItems: "center" }}>
              <Box sx={{ mt: 1 }}>
                <Typography sx={{ position: "relative", width: "1rem", height: "1rem", borderRadius: "50%", background: "#FEA89B" }}>
                  <Typography sx={{ position: "absolute", top: "-4px", left: "8px", width: "500px", color: "#000", fontWeight: 500 }}>
                    Have you taken GRE/GMAT Test?
                  </Typography>
                </Typography>
                <FormControl>
                  <RadioGroup
                    row
                    name="greTest"
                    value={isGRESelected}
                    onChange={(e: any) =>
                      eventHandler({
                        name: "academicTestSelection",
                        data: e.target.value,
                      })
                    }
                  >
                    {["Yes", "Planning to take", "Not planning to take"].map(
                      (option) => (
                        <FormControlLabel
                          key={option}
                          value={option}
                          control={<Radio />}
                          label={option}
                        />
                      )
                    )}
                  </RadioGroup>
                </FormControl>
              </Box>

              {isGRESelected === "Yes" && academicTests?.length !== academicTestNames?.length && (
                <Box sx={{ p: 2, borderRadius: "10px" }}>
                  <Select
                    displayEmpty
                    defaultValue=""
                    size="small"
                    value={selectedGRETest}
                    onChange={(e: any) =>
                      eventHandler({
                        name: "academicTest",
                        data: e.target.value,
                      })
                    }
                    sx={{ border: "1px solid #1C1F4066 !important", borderRadius: "25px", width: "150px" }}
                  >
                    <MenuItem value="">Select a test</MenuItem>
                    {Object.entries(AcademicTestNamesEnum).map(([key, value]) => {
                      const isDisabled = academicTests?.some((test) => test.name === value);
                      return (
                        <MenuItem key={key} value={value} disabled={isDisabled}>
                          {key}
                        </MenuItem>
                      );
                    })}
                  </Select>
                </Box>
              )}
            </Box>
          </Box>
          {isGRESelected === "Yes" ? <AcademicTestForm /> : null}

        </Box>
      </Stack>
    </Stack>
  );
};

export default TestScores;
