import React, { useState } from "react";
import {
  Button,
  Grid,
  Stack,
  Typography,
} from "@mui/material";
import FileUploadOutlinedIcon from "@mui/icons-material/FileUploadOutlined";
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import { setPopup } from "src/store/Slices/popupSlice";
import { useAppDispatch } from "src/assets/hooks";
import ConfirmDialog from "src/genericComponents/modalPopup/confirmDialog";
import Images from "src/assets";

const Document = ({ doc, data, inputFileRef, handleSubmit, handleDelete }: any) => {
  const [open, setOpen] = useState<boolean>(false);
  const [deleteItem, setDeleteItem] = useState<any | null>(null);
  const [identifier, setIdentifier] = useState<any | null>(null);
  
  const [loading, setLoading] = useState<boolean>(false);

  const handleDeleteOpen = (name: any, item: any) => {
    setOpen(true);
    setDeleteItem(item);
    setIdentifier(name);
  };

  const dispatch = useAppDispatch();

  const handleFileSubmit = async (
    event: React.ChangeEvent<HTMLInputElement>,
    fieldPath: string,
    fieldIdentifier: string
  ) => {
    setLoading(true); 
    await handleSubmit(event, fieldPath, fieldIdentifier);
    setLoading(false); 
  };

  return (
    <Grid
      container
      sx={{
        display: "flex",
        alignItems: "center",
        borderRadius: "8px",
        padding: "10px",
        background: "#F7FAFD",
        mb: 2,
      }}
    >
      <Grid item xs={4}>
        <Typography fontWeight="500">{doc.label}</Typography>
      </Grid>
      <Grid item xs={8} sx={{ justifyContent: "end", display: "flex" }}>
        {data && data[doc.key] ? (
          <Stack
            direction="row"
            spacing={2}
            sx={{ alignItems: "center", marginRight: "10px" }}
          >
            <Typography>{data[doc.key]?.data?.FileName}</Typography>
            <VisibilityOutlinedIcon
              sx={{ fontSize: "large" }}
              onClick={() => {
                dispatch(
                  setPopup({
                    show: true,
                    data: {
                      container: {
                        name: "fileViewer",
                        data: data[doc.key]?.data?.preview_url,
                        dimensions: {
                          width: "500px",
                        },
                      },
                      type: "custom",
                    }
                  })
                );
              }}
            />
            <DeleteOutlineOutlinedIcon
              sx={{ fontSize: "large" }}
              onClick={() => handleDeleteOpen(`personal.${doc.key}`, data[doc.key]._id)}
            />
          </Stack>
        ) : (
          <>
            <input
              style={{ display: "none" }}
              type="file"
              onChange={(event) => handleFileSubmit(event, `personal.${doc.key}`, doc.label)}
              ref={inputFileRef}
              accept=".jpeg,.png,.pdf"
            />
            {loading ? (
              <img
              src={Images.standardLoader}
              alt="load"
              style={{ width: "20px", height: "20px",marginRight:"10px"}}
            />
            ) : (
              <Button
                sx={{
                  color: "#000",
                  textTransform: "none",
                  p: 0,
                  fontSize: "0.7rem",
                }}
                onClick={() => inputFileRef.current?.click()}
              >
                <FileUploadOutlinedIcon />
              </Button>
            )}
          </>
        )}
      </Grid>
      <ConfirmDialog
        open={open}
        handleClose={() => setOpen(false)}
        additionalData={{
          onSubmit: () => handleDelete(identifier, deleteItem),
          title: "Are you sure you want to delete?",
          content: "",
        }}
      />
    </Grid>
  );
};

export default Document;
