import axios from "axios";
import React, { useRef } from "react";
import CameraAltOutlinedIcon from '@mui/icons-material/CameraAltOutlined';
import { editProfile } from "../../services";
import { setSharedInfo } from "../../store/Slices/sharedInfoSlice";
import { store } from "../../store/store";
import { useAppDispatch } from "../../assets/hooks";

interface UploadImageProps {
  accept: string;
  setLoading: (loading: boolean) => void;
}

const UploadImage: React.FC<UploadImageProps> = ({  accept, setLoading }) => {
  const prset_key = "bsjf7zim";
  const cloudName = "dffdp7skh";
  const inputFile = useRef<HTMLInputElement>(null);
  const dispatch = useAppDispatch();
  const imageClick = () => {
    if (inputFile.current) {
      inputFile.current.click();
    }
  };

  const submit = async (image: string) => {
    if (image) {
      const payload = {
        displayPicSrc: image,
      };
      setLoading(true)
      const response = await editProfile(payload);
      if (response) {
        const sharedInfoData = store.getState().sharedInfo.data;
        dispatch(
          setSharedInfo({
            ...sharedInfoData,
            displayPicSrc: response.data?.data?.displayPicSrc,
          })
        );
        setLoading(false)
        
      } else {
        setLoading(false)

      }
    }
  };

  const handleFile = (event: React.ChangeEvent<HTMLInputElement>) => {
    setLoading(true);
    const file = event.target.files?.[0];
    if (file) {
      const formData = new FormData();
      formData.append("file", file);
      formData.append("upload_preset", prset_key);
      axios
        .post(
          `https://api.cloudinary.com/v1_1/${cloudName}/image/upload`,
          formData
        )
        .then((res) => {
          const imageUrl = res.data.secure_url;
        //   onUpload(imageUrl);
          submit(imageUrl)
          setLoading(false);
        })
        .catch((err) => {
          console.error(err);
          setLoading(false);
        });
    }
  };

  return (
    <div className="flex">
      <input
        style={{ display: "none" }}
        type="file"
        className=""
        name="image"
        onChange={handleFile}
        ref={inputFile}
        accept={accept}
      />
      <h1 onClick={imageClick} className="text-center">
        {accept === ".pdf" ? "Change Image" : <CameraAltOutlinedIcon sx={{ color: "#bdd9e6" }} />}
      </h1>
    </div>
  );
};

export default UploadImage;
