import {
  // Box,
  FormControl,
  FormControlLabel,
  Grid,
  InputAdornment,
  InputLabel,
  ListSubheader,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  TextField,
  Tooltip,
} from "@mui/material";
import { Form, Formik } from "formik";
import React, { useEffect, useMemo, useState } from "react";
import CustomField from "../../../../genericComponents/customTextfield";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { useAppDispatch } from "../../../../assets/hooks";
import { editProfile } from "../../../../services";
import { setSchool } from "../../../../store/Slices/educationHistorySlice";
import { setPopup } from "../../../../store/Slices/popupSlice";
// import { grading } from "../../../../assets/menu";
import { Country, State, City } from "country-state-city";
import { Event } from "../../../../types/types";
import { CustomButton } from "../../../../genericComponents/customButton";
import InfoIcon from "@mui/icons-material/Info";
import { boards, languages } from "src/assets/menu";
import ReusableDropdown from "src/genericComponents/reusableSelect";
// import { Close } from "@mui/icons-material";
import SearchIcon from "@mui/icons-material/Search";
import DatePickerComponent from "src/genericComponents/datePicker";

interface ICountry {
  name: string;
  isoCode: string;
}

interface IState {
  name: string;
  isoCode: string;
  countryCode: string;
}

interface ICity {
  name: string;
  stateCode: string;
  countryCode: string;
}

const School = ({ data }: any) => {
  const dispatch = useAppDispatch();
  const [countries, setCountries] = useState<ICountry[]>([]);
  const [states, setStates] = useState<IState[]>([]);
  const [cities, setCities] = useState<ICity[]>([]);
  const [selectedCountry, setSelectedCountry] = useState(
    data?.country || "India"
  );
  const [selectedState, setSelectedState] = useState(
    data?.state || ""
  );
  const [selectedCity, setSelectedCity] = useState(
    data?.city || ""
  );
  const countryInfo = countries.find((c: any) => c.name === selectedCountry);
  const stateInfo = states.find((s: any) => s.name === selectedState);
  const [gradingSystem, setGradingSystem] = useState(data?.gradingSystem);
  const [totalScore, setTotalScore] = useState(data?.totalScore);
  const [customField, setCustomField] = useState<boolean>(false);
  const [board, setBoard] = useState<string>(data?.board || "");
  // const [showBoard, setShowBoard] = useState<boolean>(false);
  const handleGradingSystemChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setGradingSystem(event.target.value as string);
    setTotalScore('');
  };
  useEffect(() => {
    const fetchCountries = async () => {
      const allCountries = await Country.getAllCountries();
      setCountries(allCountries);
    };

    fetchCountries();
  }, []);

  useEffect(() => {
    const fetchStateAndCities = async () => {
      const statesOfCountry = await State.getStatesOfCountry(
        countryInfo?.isoCode
      );
      setStates(statesOfCountry);

      const citiesOfState = await City.getCitiesOfState(
        stateInfo?.countryCode ?? "",
        stateInfo?.isoCode ?? ""
      );
      setCities(citiesOfState);
    };
    fetchStateAndCities();
  }, [countryInfo, stateInfo]);

  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 150,
      },
    },
  };

  const initialValues = {
    school: {
      instituteName: data?.instituteName,
      city: selectedCity,
      state: selectedState,
      country: selectedCountry,
      languageOfInstruction: data?.languageOfInstruction,
      gradingSystem: data?.gradingSystem, // enum % grade gpa cgpa
      board: board,
      totalScore: data?.totalScore, // for grade A+..., for Percent 0-100, gpa 0-10
      startDate: data?.startDate,
      endDate: data?.endDate,
      isCompleted: data?.isCompleted,
      custom: customField

    },
  };
  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "Country":
        setSelectedCountry(event.data);
        setSelectedState('');
        setSelectedCity('');
        setCities([]);

        const countryInfo = countries.find((c: any) => c.name === event.data);
        if (countryInfo) {
          const statesOfCountry = await State.getStatesOfCountry(countryInfo.isoCode);
          setStates(statesOfCountry);
        }
        break;

      case "State":
        setSelectedCity('');
        setSelectedState(event.data);
        const stateInfo = states.find((s: any) => s.name === event.data);
        if (stateInfo) {
          const citiesOfState = await City.getCitiesOfState(stateInfo.countryCode, stateInfo.isoCode);
          setCities(citiesOfState);
        }
        break;

      case "City":
        setSelectedCity(event.data);
        break;
      default:
        break;

    }
  };
  const [searchQuery, setSearchQuery] = useState("");

  const filteredCities = useMemo(
    () =>
      cities.filter((city) =>
        city.name.toLowerCase().includes(searchQuery.toLowerCase())
      ),
    [cities, searchQuery]
  );
  const cityHandle = (newCity: string) => {
    setSelectedCity(newCity);
  };

  const submit = async (values: any) => {
    try {
      const response = await editProfile(values);
      if (response) {
        dispatch(setSchool(response.data.data.education.school))
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "success",
                data: "Grade 10 details added successfully",
                dimensions: {
                  width: "35%",
                  height: "40%",
                  show: "none"

                },
              },
              type: "custom",
            },
          })
        );
      }
    } catch (error) {

    }
  };
  return (
    <div>
      <Formik
        initialValues={initialValues}
        // validationSchema={profileSchema}
        enableReinitialize
        onSubmit={(values: any) => submit(values)}
      >
        {({ touched, errors, values, handleChange, resetForm }) => {
          return (
            <Form style={{}}>
              <Grid container spacing={3} sx={{ px: 2 }}>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Name of Institution{" "}
                  </InputLabel>
                  <CustomField
                    id="name"
                    type="text"
                    placeholder="Name of Institution"
                    name="school.instituteName"
                    value={values.school.instituteName}
                    onChange={handleChange}
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#000", fontWeight: 500 }}>
                    Education status
                  </InputLabel>
                  <FormControl>
                    <RadioGroup
                      row
                      aria-labelledby="demo-row-radio-buttons-group-label"
                      name="school.isCompleted"
                      value={values.school.isCompleted}
                      onChange={handleChange}
                    >
                      <FormControlLabel
                        value="yes"
                        control={
                          <Radio
                            size="small"
                            sx={{
                              "&.MuiRadio-root.Mui-checked": {
                                color: "#3B3F76 !important",
                              },
                            }}
                          />
                        }
                        label="Completed"
                      />
                      <FormControlLabel
                        value="no"
                        control={
                          <Radio
                            size="small"
                            sx={{
                              "&.MuiRadio-root.Mui-checked": {
                                color: "#3B3F76 !important",
                              },
                            }}
                          />
                        }
                        label="Pursuing"
                      />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#000", display: "flex", alignItems: "center" }}>
                    Board
                    <Tooltip
                      slotProps={{
                        tooltip: {
                          sx: {
                            color: "#514E6A",
                            backgroundColor: "#ffff",
                            boxShadow: 2,
                            cursor: "pointer",
                          },
                        },
                      }}
                      title={"A board in education is a governing body that manages an educational system or part of it"}
                      placement="right"
                    >
                      <InfoIcon sx={{ color: "#3B3F76", ml: 1, cursor: "pointer", fontSize: "1rem", float: "right" }} />
                    </Tooltip>
                  </InputLabel>
                  <Select
                    size="small"
                    id="Country"
                    name="school.board"
                    displayEmpty
                    defaultValue=""
                    value={board}
                    onChange={(e)=>setBoard(e.target.value)}
                    fullWidth
                    IconComponent={ExpandMoreIcon}
                    MenuProps={MenuProps}
                  >
                    <MenuItem
                      disabled
                      value=""
                      sx={{
                        "& .MuiTypography-root": {
                          fontSize: "14px !important",
                        },
                      }}
                    >
                      Select
                    </MenuItem>

                    {boards.map(
                      (name: any) => (
                        <MenuItem
                          key={name}
                          value={name}
                          sx={{
                            "& .MuiTypography-root": {
                              fontSize: "14px !important",
                            },
                            "& .MuiSvgIcon-root ": {
                              fontSize: "1rem",
                            },
                            whiteSpace: "normal",
                          }}
                        >
                          {name}
                        </MenuItem>
                      )
                    )}
                     {!boards.includes(board) && board !== "" && (
        <MenuItem key={board} value={board}>
          {board}
        </MenuItem>
      )}
        <MenuItem
                        sx={{
                          fontSize: "14px !important",
                          display: "flex",
                          alignItems: "center",
                          whiteSpace: "normal",
                          color:"#1cbeb5  !important"
                        }}
                        onClick={() => {
                          setCustomField(true)
                          dispatch(
                            setPopup({
                              show: true,
                              data: {
                                container: {
                                  name: "customfield",
                                  data: {
                                    name:"school.board",
                                    fieldName:board,
                                    setFieldName:setBoard,
                                    placeholder:"Enter your board"
                                  },
                                  dimensions: {
                                    width: "450px",
                                  },
                                },
                                type: "custom",
                              },
                            })
                          )
                        }}
                      >
                        Can't find your board
                      </MenuItem>
                  </Select>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Grading System
                  </InputLabel>

                  <Select
                    id="Country"
                    size="small"
                    name="school.gradingSystem"
                    displayEmpty
                    defaultValue=""
                    value={gradingSystem}
                    onChange={(e: any) => handleGradingSystemChange(e)}
                    fullWidth
                    IconComponent={ExpandMoreIcon}
                  >
                    <MenuItem value="" disabled>Select grade</MenuItem>
                    <MenuItem value="percentage">Percentage</MenuItem>
                    <MenuItem value="gpa">CGPA    4.0 scale</MenuItem>
                    <MenuItem value="cgpa">CGPA   10.0 scale</MenuItem>
                    <MenuItem value="grade">Grade</MenuItem>
                  </Select>
                  {/* <ErrorMessage
                      name="school.gradingSystem"
                      component="div"
                      className="error"
                    /> */}
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Score{" "}
                  </InputLabel>
                  {gradingSystem === "grade" ?
                    <Select
                      size="small"
                      id="totalScore"
                      name="school.totalScore"
                      displayEmpty
                      value={totalScore}
                      onChange={(e) => setTotalScore(e.target.value)}
                      fullWidth
                      IconComponent={ExpandMoreIcon}
                      MenuProps={MenuProps}
                    >
                      <MenuItem disabled value="">
                        Select Grade
                      </MenuItem>
                      {["A", "A+", "B", "B+", "C", "C+", "D", "D+"].map((name: string) => (
                        <MenuItem key={name} value={name}>
                          {name}
                        </MenuItem>
                      ))}
                    </Select>
                    :
                    <TextField
                      // type="number"
                      name="school.totalScore"
                      size="small"
                      fullWidth
                      value={totalScore}
                      onChange={(e) => setTotalScore(e.target.value)}
                      placeholder={
                        gradingSystem === "percentage"
                          ? "Enter percentage"
                          : gradingSystem === "grade"
                            ? "Enter grade"
                            : "Enter score"
                      }
                      inputProps={{
                        step: "0.1",

                        min: gradingSystem === "percentage" ? 1 : gradingSystem === "gpa" ? 1 : gradingSystem === "cgpa" ? 1 : undefined,
                        max: gradingSystem === "percentage" ? 100 : gradingSystem === "gpa" ? 4 : gradingSystem === "cgpa" ? 10 : undefined,
                        list: gradingSystem === "grade" ? "grades" : undefined,
                        onInput: (e) => {
                          const target = e.target as HTMLInputElement;
                          const value = parseFloat(target.value);
                          if (gradingSystem !== "Grade") {
                            const min = gradingSystem === "percentage" ? 1 : gradingSystem === "gpa" ? 1 : gradingSystem === "cgpa" ? 1 : undefined;
                            const max = gradingSystem === "percentage" ? 100 : gradingSystem === "gpa" ? 4 : gradingSystem === "cgpa" ? 10 : undefined;

                            if (value < (min || 0) || value > (max || Infinity)) {
                              target.value = value.toString().slice(0, -1); // Remove the last character
                            }
                          }
                        },
                      }}
                    />}

                  {/* <ErrorMessage
                    name="school.totalScore"
                    component="div"
                    className="error"
                  /> */}
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Language of instruction
                  </InputLabel>
                  <ReusableDropdown
                    label="Language of Instruction"
                    name="school.languageOfInstruction"
                    options={languages}
                    value={values.school.languageOfInstruction}
                    onChange={handleChange}
                    placeholder="Select"
                    searchEnabled={true}  // Enables the search box
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Start Date
                  </InputLabel>
                  <DatePickerComponent
                    selectedDate={values?.school?.startDate}
                    onChange={(date) => handleChange({ target: { name: `school.startDate`, value: date?.toISOString().slice(0, 10) } })}
                    maxDate={new Date()}
                    placeholderText="Start Date"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  {values.school.isCompleted === "yes" && (
                    <>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                      >
                        End Date
                      </InputLabel>
                      <DatePickerComponent
                        selectedDate={values?.school?.endDate}
                        onChange={(date) =>
                          handleChange({
                            target: {
                              name: `school.endDate`,
                              value: date?.toISOString().slice(0, 10),
                            },
                          })
                        }
                        minDate={
                          values.school.startDate
                            ? new Date(
                              new Date(values.school.startDate).setFullYear(
                                new Date(values.school.startDate).getFullYear() + 1
                              )
                            )
                            : undefined
                        }
                        maxDate={
                          values.school.startDate
                            ? (() => {
                                const fourYearsFromStartDate = new Date(
                                  new Date(values.school.startDate).setFullYear(
                                    new Date(values.school.startDate).getFullYear() + 2
                                  )
                                );
                                return fourYearsFromStartDate > new Date() ? new Date() : fourYearsFromStartDate;
                              })()
                            : new Date()
                        }
                        placeholderText="End Date"
                      />
                    </>
                  )}
                </Grid>

                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Country
                  </InputLabel>
                  <ReusableDropdown
                    label="Country"
                    name="school.country"
                    options={countries.map(country => country.name)}
                    value={selectedCountry}
                    onChange={(e: any) => eventHandler({
                      name: "Country",
                      data: e?.target.value,
                    })}
                    placeholder="Select"
                    searchEnabled={true}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    State/Province
                  </InputLabel>
                  <ReusableDropdown
                    label="State"
                    name="school.state"
                    options={states.map(state => state.name)}
                    value={selectedState}
                    onChange={(e: any) => eventHandler({
                      name: "State",
                      data: e?.target.value,
                    })}
                    placeholder="Select"
                    searchEnabled={true}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    City/Town
                  </InputLabel>
                  <FormControl fullWidth>
                    <Select
                      id="city"
                      name="school.city"
                      value={selectedCity}
                      displayEmpty
                      size="small"
                      defaultValue=""
                      onChange={(e) => setSelectedCity(e.target.value)}
                      fullWidth
                      IconComponent={ExpandMoreIcon}
                      onClose={() => setSearchQuery("")} // Reset search when dropdown closes
                      MenuProps={{
                        PaperProps: {
                          sx: {
                            maxHeight: 300,
                            "&.MuiPaper-root": {
                              boxShadow: 3,
                              borderRadius: "15px !important",
                            },
                          },
                        },
                      }}
                    >
                      <ListSubheader>
                        <TextField
                          size="small"
                          autoFocus
                          placeholder="Search City..."
                          fullWidth
                          autoComplete="off"
                          // autoFocus="true"
                          value={searchQuery}
                          onChange={(e) => setSearchQuery(e.target.value)}
                          InputProps={{
                            startAdornment: (
                              <InputAdornment position="start">
                                <SearchIcon />
                              </InputAdornment>
                            ),
                          }}
                          onKeyDown={(e) => {
                            if (e.key !== "Escape") {
                              e.stopPropagation();
                            }
                          }}
                        />
                      </ListSubheader>

                      <MenuItem value="" disabled>
                        {!selectedState ? "Please select state" :
                          "Select city"}
                      </MenuItem>

                      {filteredCities.length > 0 ? (
                        filteredCities.map((city: any) => (
                          <MenuItem key={city.name} value={city.name}>
                            {city.name}
                          </MenuItem>
                        ))
                      ) : (
                        selectedState && <MenuItem disabled>No cities available</MenuItem>
                      )}

                      {!cities.some((city) => city.name === selectedCity) &&
                        selectedCity !== undefined &&
                        selectedCity !== "" && (
                          <MenuItem key={selectedCity} value={selectedCity}>
                            {selectedCity}
                          </MenuItem>
                        )}

                      <MenuItem
                        sx={{
                          fontSize: "14px !important",
                          display: "flex",
                          alignItems: "center",
                          whiteSpace: "normal",
                          color: "#1cbeb5 !important",
                        }}
                        // value=""
                        onClick={() => {
                          // setSelectedCity("")
                          dispatch(
                            setPopup({
                              show: true,
                              data: {
                                container: {
                                  name: "customfield",
                                  data: {
                                    name: "school.city",
                                    fieldName: selectedCity,
                                    setFieldName: cityHandle,
                                    placeholder: "Enter your city",
                                  },
                                  dimensions: {
                                    width: "450px",
                                  },
                                },
                                type: "custom",
                                // eventHandler:cityHandle
                              },
                            })
                          );
                        }}
                      >
                        Can't find city
                      </MenuItem>
                    </Select>
                  </FormControl>

                </Grid>

                <Grid
                  item
                  xs={12}
                  sx={{ display: "flex", justifyContent: "end", my: 2, mr: 10 }}
                >
                  <CustomButton
                    handleSubmit={() => submit}
                    width="100px"
                  >
                    Save
                  </CustomButton>
                </Grid>
              </Grid>
            </Form>
          );
        }}
      </Formik>
    </div>
  );
};

export default School;
