import {
  Box,
  Button,
  Grid,
  Stack,
  Typography,
} from "@mui/material";
// eslint-disable-next-line
import React, { useRef, useState } from "react";
import { AcademicTestNamesEnum } from "../../../../assets/enums";
import { editProfile, uploadProfile } from "../../../../services";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import { settestScores } from "../../../../store/Slices/testScoresSlice";
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import { setPopup } from "../../../../store/Slices/popupSlice";
import { Test } from "../../../../types/types";
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import ConfirmDialog from "src/genericComponents/modalPopup/confirmDialog";
import Images from "src/assets";
import { setDocuments } from "src/store/Slices/documentsSlice";

const AcademicTestForm = () => {
  const dispatch = useAppDispatch();
  const testscoresData = useAppSelector((state) => state.testscores);
  const [open, setOpen] = useState<boolean>(false);
  const [deleteItem, setDeleteItem] = useState<any | null>(null);
  const [loadingMap, setLoadingMap] = useState<{ [key: string]: boolean }>(
    {}
  );
  const inputRefs = useRef<{ [key: string]: React.RefObject<HTMLInputElement> }>({});
  const handleDeleteOpen = (item: any) => {
    setOpen(true);
    setDeleteItem(item);
  };

  const academicTestNames = Object.values(AcademicTestNamesEnum);

  const filteredTests = testscoresData.data?.filter((test) =>
    academicTestNames.includes(test.name)
  );
  const handleTestsDelete = async (item: any) => {
    const updated = testscoresData?.data?.filter(
      (value: any) => value?._id !== item._id
    );
    const response = await editProfile({ tests: updated });
    if (response) {
      dispatch(settestScores(response.data.data.tests));
    } else {
    }
    setOpen(false)
  };
  const documentUpload = async (
    event: React.ChangeEvent<HTMLInputElement>,
    fieldPath: string,
    item: any
  ) => {
    const file = event.target.files && event.target.files[0];
    if (!file) return;
    setLoadingMap((prevLoadingMap) => ({
      ...prevLoadingMap,
      [item]: true,
    }));

    try {
      const formData = new FormData();
      formData.append("uploaded_file", file);
      formData.append("fieldPath", fieldPath);

      const response = await uploadProfile(formData);

      if (response) {
        dispatch(setDocuments(response.data.data.docs));

        const lastDoc = response.data.data.docs.test.general.at(-1);
        const updatedData: Test = {
          ...item,
          docId: lastDoc._id,
        };

        const updatedTestscoresData = testscoresData.data.map((test) =>
          test._id === updatedData._id ? updatedData : test
        );

        const response1 = await editProfile({ tests: updatedTestscoresData });
        if (response1) {
          dispatch(settestScores(response1.data.data.tests));
        }
      }
    } catch (err) {
      console.error(err);
    } finally {
      setLoadingMap((prevLoadingMap) => ({
        ...prevLoadingMap,
        [item]: false,
      }));
    }
  };

  return (
    <div>
      <Grid container spacing={2} sx={{mb:2}}>
        {filteredTests?.map((item: Test, i: number) => {
            if (!inputRefs.current[item._id]) {
              inputRefs.current[item._id] = React.createRef<HTMLInputElement>();
            }
            return (
          <Grid item xs={12} sm={6} lg={4} key={i}>
            <Box sx={{ boxShadow: "0px 0px 8px 0px #0000001A", p: 2, mt: 2, borderRadius: "15px" }}>
              <Stack direction="row" justifyContent="space-between">
                 <Typography sx={{ position: "relative", width: "1rem", height: "1rem", borderRadius: "50%", background: "#72D4D1" }}>
                 <Typography fontWeight="500" sx={{ position: "absolute", top: "-4px", left: "8px", width: "200px" }}>{Object.keys(AcademicTestNamesEnum).find(
                  (key) => AcademicTestNamesEnum[key as keyof typeof AcademicTestNamesEnum] === item.name
                )}</Typography>
            </Typography>
                <Box sx={{ display: "flex", gap: "0.5rem" }}>
                  {item?.docId ?
                   <Stack
                   direction="row"
                   spacing={2}
                   sx={{ alignItems: "center", marginRight: "10px" }}
                 >
                   <Typography>{item?.docId?.data?.FileName}</Typography>
                   <VisibilityOutlinedIcon
                     sx={{ fontSize: "large",cursor:"pointer" }}
                     onClick={() => {
                       dispatch(
                         setPopup({
                           show: true,
                           data: {
                             container: {
                               name: "fileViewer",
                               data: item?.docId?.data?.preview_url,
                               dimensions: { width: "500px" },
                             },
                             type: "custom",
                           },
                         })
                       );
                     }}
                   />
                 </Stack>
                    :
                    <>
                    <input
                      style={{ display: "none" }}
                      type="file"
                      className=""
                      name="docId"
                      onChange={(event) => documentUpload(event, "test.general",item._id)}
                      ref={inputRefs.current[item._id]}
                      accept=".pdf,.jpeg,.png"
                    />
                   <Button
                      sx={{
                        color: "#000",
                        textTransform: "none",
                        border: "1px solid #000",
                        borderRadius: "15px",
                        fontSize: "0.7rem",
                        p: 0,
                        mr: 3,
                      }}
                      onClick={() =>
                        inputRefs.current[item._id]?.current?.click()
                      }
                    >
                      {loadingMap[item._id] ? (
                        <Box
                          component="img"
                          src={Images.standardLoader}
                          alt="standardLoader"
                          sx={{ width: "1rem", height: "1rem" }}
                        />
                      ) : (
                        "Upload"
                      )}
                    </Button>
                  </>

                    }
                </Box>
              </Stack>
              <Grid container spacing={1}>
                {item?.scores?.map((score, j) => (
                  <Grid item xs={12} key={j} sx={{ textAlign: "center" }}>
                    <Grid container>
                      <Grid item xs={8}>
                        <Typography
                          sx={{
                            fontSize: { xs: "13px", sm: "16px" },
                            textAlign: "start",
                            color: "#00000080"
                          }}
                        >
                          {score?.description}
                        </Typography>
                      </Grid>
                      <Grid item xs={3}>
                        <Typography fontWeight="500">{score?.count} </Typography>
                      </Grid>
                    </Grid>
                  </Grid>
                ))}
                <Grid item xs={12}>
                  <Grid container>
                    <Grid item xs={9}>
                      <Typography
                        sx={{
                          fontSize: { xs: "13px", sm: "16px" },
                          textAlign: "start",
                          color: "#00000080"
                        }}
                      >
                        Took test on
                      </Typography>
                    </Grid>
                    <Grid item xs={3}>
                      <Typography fontWeight="500">
                        {item?.testDate ? 
                        new Date(item?.testDate).toLocaleString(
                          "en-GB",
                          {
                            month: "short",
                            year: "numeric",
                          }
                        )
                         : "N/A"}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12}>
                  <Grid container>
                    <Grid item xs={9}>
                    </Grid>
                    <Grid item xs={3} sx={{ display: "flex", gap: "0.5rem" }}>
                      <EditOutlinedIcon sx={{ fontSize: "large", cursor: "pointer" }}
                        onClick={() =>
                          dispatch(
                            setPopup({
                              show: true,
                              data: {
                                container: {
                                  name: "edittest",
                                  data: { data: item, name: "test.general" },
                                  dimensions: {
                                    width: "800px",
                                    height:"50%"
                                  },
                                },
                                type: "custom",
                              },
                            })
                          )
                        }
                      />
                      <img src={Images.delete} alt="del" style={{ width: "20px", cursor: "pointer" }} onClick={() => handleDeleteOpen(item)} />

                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
             
            </Box>
          </Grid>
          )})}
      </Grid>
      <ConfirmDialog
        open={open}
        handleClose={() => setOpen(false)}
        additionalData={{
          onSubmit: () => handleTestsDelete(deleteItem),
          title: "Are you sure you want to delete ?",
          content:
            "",
        }}
      />
    </div>
  );
};

export default AcademicTestForm;
