import React, { useRef, useState} from "react";
import {
  Button,
  Grid,
  Stack,
  Typography,
} from "@mui/material";
import FileUploadOutlinedIcon from "@mui/icons-material/FileUploadOutlined";
import Accordion from "../../../../genericComponents/accordian";
import { useAppDispatch } from "../../../../assets/hooks";
import { deleteUploadProfile, uploadProfile } from "../../../../services";
import { setDocuments } from "../../../../store/Slices/documentsSlice";
// import { Link } from "react-router-dom";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { allowedFileTypes } from "../../../../assets/menu";
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import ConfirmDialog from "src/genericComponents/modalPopup/confirmDialog";
import Images from "src/assets";

const AcademicDocs = ({ data }: any) => {
  const inputFileRefs = {
    secondarySchool: useRef<HTMLInputElement>(null),
    plus2: useRef<HTMLInputElement>(null),
    degree: useRef<HTMLInputElement>(null),
  };
  const [open, setOpen] = useState<boolean>(false);
  const [deleteItem, setDeleteItem] = useState<any | null>(null);
  const [identifier, setIdentifier] = useState<any | null>(null);
  
  const [loadingState, setLoadingState] = useState<{ [key: string]: boolean }>({});
  const handleDeleteOpen = (name: any, item: any) => {
    setOpen(true);
    setDeleteItem(item);
    setIdentifier(name);
  };

  const dispatch = useAppDispatch();

  const handleFileSubmit = async (
    event: React.ChangeEvent<HTMLInputElement>,
    fieldPath: string,
    fieldIdentifier: string
  ) => {
    setLoadingState((prevState) => ({ ...prevState, [fieldIdentifier]: true }));
    await handleSubmit(event, fieldPath, fieldIdentifier);
    setLoadingState((prevState) => ({ ...prevState, [fieldIdentifier]: false }));
  };
  const handleSubmit = async (
    event: React.ChangeEvent<HTMLInputElement>,
    fieldPath: string,
    fieldIdentifier:string

  ) => {
    const file = event.target.files && event.target.files[0];
    if (file && allowedFileTypes.includes(file.type)) {
      try {
        if (fieldPath && file !== null && file !== undefined) {
          const formData = new FormData();
          formData.append("uploaded_file", file);
          formData.append("fieldPath", fieldPath);
        formData.append("fileIdentifier", fieldIdentifier);

          const response = await uploadProfile(formData);
          if (response) {
            dispatch(setDocuments(response.data.data.docs));
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "success",
                    data: "Document added successfully",
                    dimensions: {
                      width: "30%",
                      height:"40%",
                      show:"none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } else {
        }
      } catch (err) {}
    } else {
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "error",
              data: "Only jpeg , png , pdf files are accepted",
              dimensions: {
                width: "250px",
                show:"none"

              },
            },
            type: "custom",
          },
        })
      );
    }
  };

  const handleDelete = async (fieldPath: string, deleteId: string) => {
    const response = await deleteUploadProfile({
      fieldPath: fieldPath,
      documentId: deleteId,
    });
    if (response) {
      dispatch(setDocuments(response.data.data));
    } else {
    }
  };

  const documents = [
    { key: "transcripts", label: "Transcripts" },
    { key: "bonafide", label: "Bonafide" },
    { key: "CMM", label: "CMM" },
    { key: "PCM", label: "PCM" },
    { key: "OD", label: "OD" },
  ];
  const secondarydocuments = [
    { key: "secondarySchool", label: "School" },
    { key: "plus2", label: "Grade 12" },
    { key: "degree", label: "Degree" },
  ];
  const Bachelors = () => {
    const dispatch = useAppDispatch();
  
    const bachelorsFileRefs = {
      transcripts: useRef<HTMLInputElement>(null),
      bonafide: useRef<HTMLInputElement>(null),
      CMM: useRef<HTMLInputElement>(null),
      PCM: useRef<HTMLInputElement>(null),
      OD: useRef<HTMLInputElement>(null),
    };
  
    const [loadingState, setLoadingState] = useState<{ [key: string]: boolean }>({});
  
    const [open, setOpen] = useState<boolean>(false);
    const [deleteItem, setDeleteItem] = useState<any | null>(null);
    const [identifier, setIdentifier] = useState<any | null>(null);
  
    const handleDeleteOpen = (name: any, item: any) => {
      setOpen(true);
      setDeleteItem(item);
      setIdentifier(name);
    };
  
    const handleFileSubmit = async (
      event: React.ChangeEvent<HTMLInputElement>,
      fieldPath: string,
      fieldIdentifier: string
    ) => {
      setLoadingState((prevState) => ({ ...prevState, [fieldIdentifier]: true }));
      await handleSubmit(event, fieldPath, fieldIdentifier);
      setLoadingState((prevState) => ({ ...prevState, [fieldIdentifier]: false }));
    };
  
    const DocumentItem = ({ doc, data, handleSubmit, handleDelete, inputFileRefs }: any) => {
      return (
        <Grid
          container
          sx={{
            display: "flex",
            alignItems: "center",
            borderRadius: "8px",
            padding: "10px",
            background: "#F7FAFD",
            mb: 2,
          }}
        >
          <Grid item xs={4}>
            <Typography fontWeight="500">{doc.label}</Typography>
          </Grid>
          <Grid item xs={8} sx={{ justifyContent: "end", display: "flex" }}>
            {data && data[doc.key] ? (
              <Stack
                direction="row"
                spacing={2}
                sx={{ alignItems: "center", marginRight: "10px" }}
              >
                <Typography>{data[doc.key]?.data?.FileName}</Typography>
                <VisibilityOutlinedIcon
                  sx={{ fontSize: "large" }}
                  onClick={() => {
                    dispatch(
                      setPopup({
                        show: true,
                        data: {
                          container: {
                            name: "fileViewer",
                            data: data[doc.key]?.data?.preview_url,
                            dimensions: { width: "500px" },
                          },
                          type: "custom",
                        },
                      })
                    );
                  }}
                />
                <DeleteOutlineOutlinedIcon
                  sx={{ fontSize: "large" }}
                  onClick={() =>
                    handleDeleteOpen(`academic.bachelors.${doc.key}`, data[doc.key]._id)
                  }
                />
              </Stack>
            ) : (
              <>
                <input
                  style={{ display: "none" }}
                  type="file"
                  onChange={(event) => handleFileSubmit(event, `academic.bachelors.${doc.key}`, doc.key)}
                  ref={inputFileRefs[doc.key as keyof typeof inputFileRefs]}
                  accept=".pdf,.png,.jpeg"
                />
                {loadingState[doc.key] ? (
                   <img
                   src={Images.standardLoader}
                   alt="load"
                   style={{ width: "20px", height: "20px",marginRight:"10px"}}
                 />
                ) : (
                  <Button
                    sx={{
                      color: "#000",
                      textTransform: "none",
                      p: 0,
                      fontSize: "0.7rem",
                    }}
                    onClick={() =>
                      inputFileRefs[doc.key as keyof typeof inputFileRefs].current?.click()
                    }
                  >
                    <FileUploadOutlinedIcon />
                  </Button>
                )}
              </>
            )}
          </Grid>

        </Grid>
      );
    };
  
    return (
      <>
      <Accordion title="Bachelors">
        <>
          {documents.map((doc) => (
            <DocumentItem
              key={doc.key}
              doc={doc}
              data={data.bachelors}
              handleSubmit={handleSubmit}
              handleDelete={handleDelete}
              inputFileRefs={bachelorsFileRefs}
            />
          ))}
        </>
      </Accordion>
                <ConfirmDialog
                open={open}
                handleClose={() => setOpen(false)}
                additionalData={{
                  onSubmit: () => handleDelete(identifier, deleteItem),
                  title: "Are you sure you want to delete?",
                  content: "",
                }}
              />
              </>
    );
  };
  
  const Masters = () => {
    const dispatch = useAppDispatch();
  
    const mastersFileRefs = {
      transcripts: useRef<HTMLInputElement>(null),
      bonafide: useRef<HTMLInputElement>(null),
      CMM: useRef<HTMLInputElement>(null),
      PCM: useRef<HTMLInputElement>(null),
      OD: useRef<HTMLInputElement>(null),
    };
  
    const [loadingState, setLoadingState] = useState<{ [key: string]: boolean }>({});
  
    const [open, setOpen] = useState<boolean>(false);
    const [deleteItem, setDeleteItem] = useState<any | null>(null);
    const [identifier, setIdentifier] = useState<any | null>(null);
  
    const handleDeleteOpen = (name: any, item: any) => {
      setOpen(true);
      setDeleteItem(item);
      setIdentifier(name);
    };
  
    const handleFileSubmit = async (
      event: React.ChangeEvent<HTMLInputElement>,
      fieldPath: string,
      fieldIdentifier: string
    ) => {
      setLoadingState((prevState) => ({ ...prevState, [fieldIdentifier]: true }));
      await handleSubmit(event, fieldPath, fieldIdentifier);
      setLoadingState((prevState) => ({ ...prevState, [fieldIdentifier]: false }));
    };
  
    const DocumentItem = ({ doc, data, handleSubmit, handleDelete, inputFileRefs }: any) => {
      return (
        <Grid
          container
          sx={{
            display: "flex",
            alignItems: "center",
            borderRadius: "8px",
            padding: "10px",
            background: "#F7FAFD",
            mb: 2,
          }}
        >
          <Grid item xs={4}>
            <Typography fontWeight="500">{doc.label}</Typography>
          </Grid>
          <Grid item xs={8} sx={{ justifyContent: "end", display: "flex" }}>
            {data && data[doc.key] ? (
              <Stack
                direction="row"
                spacing={2}
                sx={{ alignItems: "center", marginRight: "10px" }}
              >
                <Typography>{data[doc.key]?.data?.FileName}</Typography>
                <VisibilityOutlinedIcon
                  sx={{ fontSize: "large" }}
                  onClick={() => {
                    dispatch(
                      setPopup({
                        show: true,
                        data: {
                          container: {
                            name: "fileViewer",
                            data: data[doc.key]?.data?.preview_url,
                            dimensions: { width: "500px" },
                          },
                          type: "custom",
                        },
                      })
                    );
                  }}
                />
                <DeleteOutlineOutlinedIcon
                  sx={{ fontSize: "large" }}
                  onClick={() =>
                    handleDeleteOpen(`academic.masters.${doc.key}`, data[doc.key]._id)
                  }
                />
              </Stack>
            ) : (
              <>
                <input
                  style={{ display: "none" }}
                  type="file"
                  onChange={(event) => handleFileSubmit(event, `academic.masters.${doc.key}`, doc.key)}
                  ref={inputFileRefs[doc.key as keyof typeof inputFileRefs]}
                  accept=".pdf,.png,.jpeg"
                />
                {loadingState[doc.key] ? (
                   <img
                   src={Images.standardLoader}
                   alt="load"
                   style={{ width: "20px", height: "20px",marginRight:"10px"}}
                 />
                ) : (
                  <Button
                    sx={{
                      color: "#000",
                      textTransform: "none",
                      p: 0,
                      fontSize: "0.7rem",
                    }}
                    onClick={() =>
                      inputFileRefs[doc.key as keyof typeof inputFileRefs].current?.click()
                    }
                  >
                    <FileUploadOutlinedIcon />
                  </Button>
                )}
              </>
            )}
          </Grid>

        </Grid>
      );
    };
  
    return (
      <>
      <Accordion title="Masters">
        <>
          {documents.map((doc) => (
            <DocumentItem
              key={doc.key}
              doc={doc}
              data={data.masters}
              handleSubmit={handleSubmit}
              handleDelete={handleDelete}
              inputFileRefs={mastersFileRefs}
            />
          ))}
        </>
      </Accordion>
                <ConfirmDialog
                open={open}
                handleClose={() => setOpen(false)}
                additionalData={{
                  onSubmit: () => handleDelete(identifier, deleteItem),
                  title: "Are you sure you want to delete?",
                  content: "",
                }}
              />
              </>
    );
  };

  const Document = ({ doc, data, inputFileRef, handleSubmit, handleDelete }:any) => {

  
    return (
      <Grid
        container
        sx={{
          display: "flex",
          alignItems: "center",
          borderRadius: "8px",
          padding: "10px",
          background: "#F7FAFD",
          mb: 2,
        }}
      >
        <Grid item xs={4}>
          <Typography fontWeight="500">{doc.label}</Typography>
        </Grid>
        <Grid item xs={8} sx={{ justifyContent: "end", display: "flex" }}>
          {data && data[doc.key] ? (
            <Stack
              direction="row"
              spacing={2}
              sx={{ alignItems: "center", marginRight: "10px" }}
            >
              <Typography>{data[doc.key].data?.FileName}</Typography>

                                        <VisibilityOutlinedIcon sx={{fontSize:"large"}} onClick={()=>{
                  dispatch(
                    setPopup({
                      show: true,
                      data: {
                        container: {
                          name: "fileViewer",
                          data:data[doc.key]?.data?.preview_url,
                          dimensions: {
                            width: "500px",
                          },
                        },
                        type: "custom",
                      },
                    })
                  )
              }}/>

                  <DeleteOutlineOutlinedIcon sx={{fontSize:"large"}} onClick={() => handleDeleteOpen(`academic.${doc.key}`, data[doc.key]._id)}/>
            
            </Stack>
          ) : (
            <>
              <input
                style={{ display: "none" }}
                type="file"
                onChange={(event) => handleFileSubmit(event, `academic.${doc.key}`,doc.key)}
                ref={inputFileRef}
                accept=".jpeg,.png,.pdf"
              />
              {loadingState[doc.key]  ? (
              <img
              src={Images.standardLoader}
              alt="load"
              style={{ width: "20px", height: "20px",marginRight:"10px"}}
            />
            ) : (
              <Button
                sx={{
                  color: "#000",
                  textTransform: "none",
                  p: 0,
                  fontSize: "0.7rem",
                }}
                onClick={() => inputFileRef.current?.click()}
              >
                <FileUploadOutlinedIcon />
              </Button>
            )}
            </>
          )}
        </Grid>
      </Grid>
    );
  };
  return (
    <div>
      <Typography textAlign="right">
        <b>Note:</b> Only jpeg , png , pdf files are accepted
      </Typography>
      <Masters />
      <Bachelors />
      <>
        {secondarydocuments.map((doc) => (
           <Document
           key={doc.key}
           doc={doc}
           data={data}
           inputFileRef={inputFileRefs[doc.key as keyof typeof inputFileRefs]}
           handleSubmit={handleSubmit}
           handleDelete={handleDelete}
         />
        ))}
      </>
      <ConfirmDialog
        open={open}
        handleClose={() => setOpen(false)}
        additionalData={{
          onSubmit: () => handleDelete(identifier, deleteItem),
          title: "Are you sure you want to delete?",
          content: "",
        }}
      />
    </div>
  );
};

export default AcademicDocs;
