import { Avatar, Box, Typography, Grid, Button } from "@mui/material";
import React from "react";
import { useAppDispatch, useAppSelector } from "../../../assets/hooks";
import { getAdvisorTitle, setWordCase } from "../../../assets/library";
import { requestAdvisor } from "../../../services";
import { addAdvisorInfo, updateAdvisorInfo } from "../../../store/Slices/advisorsSlice";
import { addChats } from "../../../store/Slices/chatsSlice";
import { Advisor } from "../../../types/types";
import { setPopup } from "../../../store/Slices/popupSlice";
// import Images from "src/assets";
// import { globalButton } from "src/components/styles/customStyles";
// import { useNavigate } from "react-router-dom";
import CustomCard from "src/genericComponents/customCard";

const Advisors = () => {
  const Preferences = useAppSelector((state) => state.preferences);
  const advisors = useAppSelector((state) => state.advisor);
  const dispatch = useAppDispatch();
  // const navigate = useNavigate();
  const assignedCountries =
    advisors?.data?.flatMap((advisor: Advisor) => advisor.assignedCountries) ||
    [];
  const unassignedCountries = Preferences?.data?.country?.filter(
    (country: string) => !assignedCountries.includes(country)
  );

  const addAdvisors = async (item: string) => {
    try {
      const response = await requestAdvisor({
        country: item,
      });

      const newAdvisor = response.data.data.advisor;
      const existingAdvisor = advisors?.data?.find((advisor) => advisor._id === newAdvisor._id);

      if (existingAdvisor) {
        dispatch(updateAdvisorInfo(newAdvisor));
      } else {
        dispatch(addAdvisorInfo(newAdvisor));
      }
      if (response.data.data.chat !== null) {
        dispatch(addChats(response.data.data.chat));
      }
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div>
      <Typography color="#3B3F76" gutterBottom fontSize="1.2rem">
        Advisors
      </Typography>
      <Box
        sx={{
          borderRadius: "1rem",
        }}
      >
        <Grid container spacing={3}>
          {advisors?.data?.map((item: Advisor, i: number) => (
            <Grid item xs={12} sm={6} lg={4} key={i} sx={{ cursor: "pointer" }} onClick={() =>
              dispatch(
                setPopup({
                  show: true,
                  data: {
                    container: {
                      name: "advisor",
                      data: item,
                      dimensions: {
                        width: "900px",
                        height:"85%"

                      },
                    },
                    type: "custom",
                  },
                })
              )
            }>
              <CustomCard title={getAdvisorTitle(item?.info?.role)} i={i}>
                <Box
                  sx={{
                    // backgroundColor: "#EDEDFD",
                    px: 2,
                    pb: 2,
                    borderRadius: "1rem",
                    display: "flex",
                    gap: "1rem",
                    position: "relative",
                    height: "100px"
                  }}
                >
                  <Avatar src={item?.info?.displayPicSrc} alt="" />

                  <div style={{ width: "100%" }}>
                    <Typography gutterBottom fontSize="1.2rem" >
                      {setWordCase(item?.info?.firstName)}
                    </Typography>
                  
                    {item?.info?.role === "remoteStudentAdvisor" ?
                      <Typography
                        // color="#FEB853"
                        fontSize="0.9rem"
                        sx={{ mt: 1 }}
                      >
                        Specialist in : {" "}

                        <span style={{ color: "#00000066", fontWeight: "500" }}>Providing academic guidance and career support
                        </span>
                      </Typography> : ""}
                    {item?.assignedCountries?.length > 0 ?
                      <Typography
                        // color="#FEB853"
                        fontSize="0.9rem"
                        sx={{ mt: 1 }}
                      >
                        Specialist in : {" "}
                        {item?.assignedCountries?.map((country, i) => (
                          <span key={i} style={{ color: "#00000066", fontWeight: "500" }}>{country}
                            {i < item?.assignedCountries?.length - 1 && ", "}</span>
                        ))}
                      </Typography> : ""}
                  </div>
                  {/* <p style={{
        position: 'absolute',
        right: '25px',
        top: '20%', background: "#B7B7E7", width: "1.1rem", height: "1.1rem", margin: 0, borderBottomLeftRadius: "20px", transform: "rotate(45deg"
      }}></p> */}
                </Box>
              </CustomCard>
            </Grid>
          ))}
          {unassignedCountries?.map((item, i) => (
            <Grid item xs={12} sm={6} lg={4} key={i}>
              <Box
                sx={{
                  backgroundColor: "#EDEDFD",
                  px: 2,
                  py: 4,
                  borderRadius: "1rem",
                  display: "flex",
                  gap: "1rem",
                  position: "relative",
                  flexDirection: "column",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "100px"
                }}
              >
                <Typography color="#3B3F76CC" fontSize="14px">
                  Click below to get the advisor assigned for {item}
                </Typography>
                <Button
                  onClick={() => addAdvisors(item)}
                  sx={{
                    border: "1px solid #3B3F76CC",
                    borderRadius: "15px",
                    p: 0.5,
                    textTransform: "none",
                    color: "#3B3F76CC",
                  }}
                >
                  Request for advisor
                </Button>
              </Box>
            </Grid>
          ))}

        </Grid>

      </Box>
    </div>
  );
};

export default Advisors;
