import React from "react";
import Box from "@mui/material/Box";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import { useLocation, Link } from "react-router-dom";
import {
  Avatar,
  IconButton,
  ListItemIcon,
  Tooltip,
  Typography,
  useTheme,
} from "@mui/material";
import useMediaQuery from "@mui/material/useMediaQuery";
import Images from "../../assets";
import UploadImage from "./uploadImage";
import { useAppSelector } from "../../assets/hooks";

const profileList = [
  { path: "/profile/dashboard", name: "Dashboard", icon: <img src={Images.dashboard} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/personal", name: "Profile", icon: <img src={Images.personal} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/documents", name: "Documents", icon: <img src={Images.documents} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/applications", name: "My Products", icon: <img src={Images.products} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/recommendedCourses/Safe", name: "Recommendation", icon: <img src={Images.recommendations} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/shortlisted", name: "Favourites", icon: <img src={Images.wishlist} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/advisors", name: "Advisors", icon: <img src={Images.experts} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/orders", name: "My Orders", icon: <img src={Images.orders} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/cart", name: "Cart", icon: <img src={Images.cart} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
];

const ProfileSidebar = () => {
  const location = useLocation();
  const [hovered, setHovered] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));
  const profile = useAppSelector((state) => state.sharedInfo);
  const isRecommendedCoursesActive = location.pathname.startsWith('/profile/recommendedCourses');

  return (
    <Box
      sx={{
        display: "flex",
        mr: { xs: 0, lg: 2 },
        my: 3,
        width: { xs: "100%", md: "180px", lg: "220px" },
        height: { xs: "100%" },
        justifyContent: "center",
      }}
    >
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          marginTop: "20px",
          width: { xs: "100%", md: "auto" },
        }}
      >
        <List
          sx={{
            pb: 2,
            maxWidth: "200px",
            width: "100%",
            background: "#fff",
            boxShadow: "4px 4px 50px rgb(14 4 4 / 8%)",
            borderRadius: "30px",
            height: { xs: "550px", md: "600px" },
            "& .MuiListItem-root": {
              p: 0.5,
            },
          }}
        >
          <Box sx={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", mb: 2 }}>
            <div
              style={{
                position: "relative",
                display: "inline-block",
                marginBottom: "15px",
                marginTop: "1rem",
              }}
              onMouseEnter={() => setHovered(true)}
              onMouseLeave={() => setHovered(false)}
            >
              {loading ? (
                <img
                  src={Images.standardLoader}
                  alt="loader"
                  style={{ width: "1rem" }}
                />
              ) : (
                <Avatar
                  src={profile.data?.displayPicSrc}
                  alt="profile"
                  sx={{
                    width: { xs: "2rem", md: "2.5rem" },
                    height: { xs: "2rem", md: "2.5rem" },
                    borderRadius: "50%",
                  }}
                />
              )}

              {hovered && (
                <IconButton
                  aria-label="edit"
                  style={{
                    position: "absolute",
                    top: "45%",
                    left: "50%",
                    transform: "translate(-50%, -50%)",
                    backgroundColor: "rgba(0, 0, 0, 0)",
                  }}
                >
                  <UploadImage
                    setLoading={setLoading}
                    accept=".jpg, .jpeg, .png"
                  />
                </IconButton>
              )}
            </div>
            <Typography
              color="#3B3F76"
              fontWeight="500"
              sx={{ display: { xs: "none", md: "block" } }}
            >
              {profile?.data?.firstName}
            </Typography>
          </Box>
          {profileList.map((item: any) => (
            <ListItem
              key={item.name}
              component={Link}
              to={item.path}
              sx={{
                textDecoration: "none",
                color: "#000",
                borderRadius: "20px",
              }}
            >
              <ListItemButton
                selected={location.pathname === item.path}
                sx={{
                  padding: "4px",
                  color: "#3b3f76",
                  background: "transparent!important",
                  alignItems: "center",
                }}
              >
                {isMobile ? (
                  <Tooltip title={item.name} placement="right">
                    <ListItemIcon
                      sx={{
                        color:
                          (location.pathname === item.path && !isRecommendedCoursesActive) || (item.name === "Recommendation" && (isRecommendedCoursesActive && item.path.startsWith('/profile/recommendedCourses')))
                            ? "#3b3f76"
                            : "#3b3f7691",
                        minWidth: "40px",
                        pl: 1,
                      }}
                    >
                      {item.icon}
                    </ListItemIcon>
                  </Tooltip>
                ) : (
                  <ListItemIcon
                    sx={{
                      color:
                        (location.pathname === item.path)
                          ? "#3b3f76"
                          : "#3b3f7691",
                      minWidth: "40px",
                      pl: 1,
                    }}
                  >
                    {item.icon}
                  </ListItemIcon>
                )}

                {!isMobile ? (
                  <ListItemText
                    primary={item.name}
                    sx={{
                      "& .MuiTypography-root": {
                        fontWeight: (location.pathname === item.path && !isRecommendedCoursesActive) || (item.name === "Recommendation" && (isRecommendedCoursesActive && item.path.startsWith('/profile/recommendedCourses')))
                          ? 600 : 400,
                        fontSize: { xs: "13px", md: "15px" }
                      },
                      borderBottom: (location.pathname === item.path && !isRecommendedCoursesActive) || (item.name === "Recommendation" && (isRecommendedCoursesActive && item.path.startsWith('/profile/recommendedCourses')))
                        ? "2px solid #3b3f76" : 0,
                      paddingBottom: (location.pathname === item.path && !isRecommendedCoursesActive) || (item.name === "Recommendation" && (isRecommendedCoursesActive && item.path.startsWith('/profile/recommendedCourses')))
                        ? "8px" : 0,
                      marginRight: (location.pathname === item.path && !isRecommendedCoursesActive) || (item.name === "Recommendation" && (isRecommendedCoursesActive && item.path.startsWith('/profile/recommendedCourses')))
                        ? "8px" : 0,
                    }}
                  />
                ) : null}
              </ListItemButton>
            </ListItem>
          ))}
        </List>


      </Box>
    </Box>
  );
};

export default ProfileSidebar;
