import React, { useRef } from "react";
import {
  Typography,
} from "@mui/material";
import { deleteUploadProfile, uploadProfile } from "../../../../services";
import { useAppDispatch } from "../../../../assets/hooks";
import { setDocuments } from "../../../../store/Slices/documentsSlice";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { allowedFileTypes } from "../../../../assets/menu";
import Document from "./documentPreview";

const PersonalDocs = ({ data }: any) => {
  const dispatch = useAppDispatch();
  const inputFileRefs = {
    resume: useRef<HTMLInputElement>(null),
    passportBD: useRef<HTMLInputElement>(null),
    passportADD: useRef<HTMLInputElement>(null),
  };
  const secondarydocuments = [
    { key: "resume", label: "Resume" },
    { key: "passportBD", label: "Passport Photo Page" },
    { key: "passportADD", label: "Passport Address Page" },
  ];

  const handleSubmit = async (
    event: React.ChangeEvent<HTMLInputElement>,
    fieldPath: string,
    fieldIdentifier:string
  ) => {
    const file = event.target.files && event.target.files[0];
    if (file && allowedFileTypes.includes(file.type)) {
      try {
        const formData = new FormData();
        formData.append("uploaded_file", file);
        formData.append("fieldPath", fieldPath);
        formData.append("fileIdentifier", fieldIdentifier);
        const response = await uploadProfile(formData);
        if (response) {
          dispatch(setDocuments(response.data.data.docs));
          dispatch(
            setPopup({
              show: true,
              data: {
                container: {
                  name: "success",
                  data: "Document added successfully",
                  dimensions: {
                    width: "30%",
                    height:"40%",
                    show:"none"

                  },
                },
                type: "custom",
              },
            })
          );
        }
      } catch (err) {
        console.error("Upload failed", err);
      }
    } else {
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "error",
              data: "Only jpeg, png, pdf files are accepted",
              dimensions: {
                width: "250px",
                show:"none"

              },
            },
            type: "custom",
          },
        })
      );
    }
  };

  const handleDelete = async (fieldPath: string, deleteId: string) => {
    try {
      const response = await deleteUploadProfile({ fieldPath, documentId: deleteId });
      if (response) {
        dispatch(setDocuments(response.data.data));
      }
    } catch (err) {
      console.error("Delete failed", err);
    }
  };

  return (
    <div>
      <Typography textAlign="right">
        <b>Note:</b> Only jpeg, png, pdf files are accepted
      </Typography>
      {secondarydocuments.map((doc) => (
        <Document
          key={doc.key}
          doc={doc}
          data={data}
          inputFileRef={inputFileRefs[doc.key as keyof typeof inputFileRefs]}
          handleSubmit={handleSubmit}
          handleDelete={handleDelete}
        />
      ))}
    </div>
  );
};

export default PersonalDocs;
