import {
  Box,
  Button,
  FormControlLabel,
  Grid,
  Stack,
  Switch,
  Tooltip,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { useAppDispatch, useAppSelector } from "../../../assets/hooks";
import {
  // globalButton,
  secondaryButton,
} from "../../../components/styles/customStyles";
import CustomCard from "../../../genericComponents/customCard";
import { setPopup } from "../../../store/Slices/popupSlice";
import { Event, Recommendation } from "../../../types/types";
import { monthNames } from "../../../assets/menu";
import TimerIcon from "@mui/icons-material/Timer";
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import FavoriteOutlinedIcon from '@mui/icons-material/FavoriteOutlined';
import NotInterestedOutlinedIcon from "@mui/icons-material/NotInterestedOutlined";
import {
  addToShortList,
  getRecommendations,
  hideRecommendation,
} from "../../../services";
import {
  setShortlisted,
} from "../../../store/Slices/shortlistedCoursesSlice";
import Images from "../../../assets";
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import { setRecommended } from "../../../store/Slices/recommendations";
import { CustomButton } from "../../../genericComponents/customButton";
import { DestinationTypeEnum } from "src/assets/enums";
import { useParams } from "react-router-dom";
import ConfirmDialog from "src/genericComponents/modalPopup/confirmDialog";

const Recommended = () => {
  // const queryParams = new URLSearchParams(location.search.slice(1));
  const { id }: any = useParams();
  const [activeTab, setActiveTab] = useState<string>('Safe');
  // console.log(id)

  const recommendations = useAppSelector((state) => state.recommendations);
  const criteria = recommendations?.data?.criteria;
  const preferences = useAppSelector((state) => state.preferences);
  const underGraduation = useAppSelector((state) => state.educationhistory)
  const testscores = useAppSelector(state => state.testscores)
  const gre = testscores.data.find((item) => item.name === "Graduate Record Examination")

  const isCriteriaMet =
    criteria?.sub_discipline === JSON.stringify(preferences?.data?.courses) &&
    criteria?.ug_gpa === JSON.stringify(underGraduation?.data?.underGraduation) &&
    criteria?.gre === JSON.stringify(gre?.scores);

  const ambitious = recommendations?.data && recommendations?.data?.data?.filter(
    (item: Recommendation) => item?.possibilityOfAdmit === "Ambitious"
  );
  const safe: any = recommendations?.data && recommendations?.data?.data?.filter(
    (item) => item?.possibilityOfAdmit === "Safe"
  );
  const moderate: any = recommendations?.data && recommendations?.data?.data?.filter(
    (item) => item?.possibilityOfAdmit === "Moderate"
  );
  const courseChecked = (preferences?.data?.courses?.length ?? 0) > 0;

  const [loading, setLoading] = useState(false);

  const [alignment, setAlignment] = React.useState("interested");

  const handleChange = (
    event: any
  ) => {
    setAlignment(event.target.checked ? "interested" : "notInterested");
  };
  useEffect(() => {
    if (id) {
      const matchedTab = tabs.find((tab) => tab.value.toLowerCase() === id.toLowerCase());
      if (matchedTab) {
        setActiveTab(matchedTab.value);
      }
    }
    // eslint-disable-next-line
  }, [id]);

  const Card = (props: any) => {
    const item = props?.data;
    const userAuthStatus = useAppSelector((state) => state.userAuthStatus);
    const shortlisted = useAppSelector((state) => state.shortlistedcourses);
    const dispatch = useAppDispatch();
    const [loadingMap, setLoadingMap] = useState<{ [key: string]: boolean }>(
      {}
    );
    const [open, setOpen] = React.useState(false);
    const [deleteItem, setDeleteItem] = useState()
    const eventHandler = async (event: Event) => {
      switch (event.name) {
        case "cart":
          if (
            userAuthStatus?.data?.isAuthorized &&
            userAuthStatus?.data?.isAuthorized
          ) {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "applynow",
                    data: event?.data?.course,
                    dimensions: {
                      width: "450px",
                      // show:"none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          } else {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "login",
                    dimensions: {
                      width: "800px",
                      height: "70%",
                      show: "none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
          break;
        case "shortlist":
          try {
            if (
              userAuthStatus?.data?.isAuthorized &&
              userAuthStatus?.data?.isAuthorized
            ) {
              setLoadingMap((prevLoadingMap) => ({
                ...prevLoadingMap,
                [event.data.course._id]: true,
              }));
              const values = {
                action: "push",
                courseId: event.data?.course?._id,
              };
              const response = await addToShortList(values);
              if (response.data.success) {
                dispatch(setShortlisted(response.data.data))

                setLoadingMap((prevLoadingMap) => ({
                  ...prevLoadingMap,
                  [event.data.course._id]: false,
                }));
              } else {
                setLoadingMap((prevLoadingMap) => ({
                  ...prevLoadingMap,
                  [event.data.course._id]: false,
                }));
              }
            } else {
              dispatch(
                setPopup({
                  show: true,
                  data: {
                    container: {
                      name: "login",
                      dimensions: {
                        width: "800px",
                        height: "70%",
                        show: "none"

                      },
                    },
                    type: "custom",
                  },
                })
              );
            }
          } catch (error) {
            console.error("An error occurred while shortlisting:", error);
            setLoadingMap((prevLoadingMap) => ({
              ...prevLoadingMap,
              [event.data._id]: false,
            }));
          }
          break;
        case "delete":
          try {
            setLoadingMap((prevLoadingMap) => ({
              ...prevLoadingMap,
              [event.data.course._id]: true,
            }));
            const values = {
              action: "pull",
              courseId: event.data?.course?._id,
            };

            const response = await addToShortList(values);
            if (response.data.success) {
              setLoadingMap((prevLoadingMap) => ({
                ...prevLoadingMap,
                [event.data.course?._id]: false,
              }));
              dispatch(setShortlisted(response.data.data))

            }
          } catch (error) {
            setLoadingMap((prevLoadingMap) => ({
              ...prevLoadingMap,
              [event.data.course._id]: false,
            }));
            console.error("An error occurred while unshortlisting:", error);
          }
          break;
        case "hideRecommendation":
          try {
            const response = await hideRecommendation({
              recommendationId: event.data,
            });
            dispatch(setRecommended(response.data.data));
            // console.log(response);
          } catch (err) { }
          break;
        default:
          break;
      }
    };

    const handleDelete = (item: any) => {
      setOpen(true)
      setDeleteItem(item)
    }

    const fullToShort: Record<string, string> = Object.fromEntries(
      Object.entries(DestinationTypeEnum).map(([key, value]) => [value, key])
    );

    return (
      <div>
        <CustomCard title={item?.counsellorRecommended === true ? "Counsellor Recommended" : "AI recommended"} i={props.i}>
          <Box
            onClick={() => {
              dispatch(
                setPopup({
                  show: true,
                  data: {
                    container: {
                      name: "mainPopup",
                      data: {
                        ...item.course,
                        university: {
                          ...item?.course?.university,
                        },
                      },
                      dimensions: {
                        width: "90%",
                        // height:"80%"
                      },
                    },
                    type: "custom",
                  },
                })
              );
            }}
          >
            <Box sx={{ display: "flex", gap: "10px", width: "100%" }}>
              <img
                src={item.course?.university?.logoSrc}
                alt="logo"
                style={{
                  width: "1.5rem",
                  height: "1.5rem",
                  marginRight: "5px",
                  borderRadius: "10px",
                }}
              />
              <div style={{ flex: 1 }}>
                <Tooltip
                  slotProps={{
                    tooltip: {
                      sx: {
                        color: "#fff",
                        backgroundColor: "#3B3F76",
                        boxShadow: 1,
                        "&.MuiTooltip-tooltip": {
                          marginTop: "-5px !important",
                        },
                      },
                    },
                  }}
                  title={item?.course?.name}
                  placement="bottom"
                >
                  <Typography
                    fontSize="0.9rem"
                    sx={{
                      fontSize: { xs: "13px", lg: "14px" },
                      fontWeight: 500,
                      textOverflow: "ellipsis ",
                      lineClamp: 1,
                      height: "1.5rem",
                      textTransform: "capitalize",
                      overflow: "hidden",
                      display: "-webkit-box",
                      WebkitLineClamp: "1",
                      WebkitBoxOrient: "vertical",
                    }}
                  >
                    {item?.course?.name}
                  </Typography>
                </Tooltip>
                <Typography fontSize="0.8rem" color="#8D8B8B">
                  {item.course?.university?.name} ,&nbsp;
                  {fullToShort[item?.course?.university?.location?.country]}
                </Typography>
              </div>

            </Box>
            <Stack sx={{ my: 1, marginLeft: "2rem" }} spacing={1}>


              <Stack
                direction="row"
                alignItems="center"
                spacing={1}
                sx={{
                  flexWrap: "nowrap",
                  height: "1.5rem",
                  fontWeight: 600,
                  textOverflow: "ellipsis",
                  whiteSpace: "nowrap",
                  overflow: "hidden",
                }}
              >
                <Typography
                  component="span"
                  fontSize="0.8rem"
                  // fontWeight="500"
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    whiteSpace: "nowrap",
                  }}
                >
                  <TimerIcon sx={{ fontSize: "1.25rem" }} />
                  &nbsp; Start date :
                  {" " +
                    item?.course?.startDate
                      ?.map(
                        (item: any, index: number) =>
                          monthNames[item?.courseStartingMonth]
                      )
                      .join(", ")}
                  {/* {item?.intake ? date(new Date(item?.intake)) : null} */}
                </Typography>
                <Typography fontSize="0.8rem">
                  {"| "}
                  {item.course.studyLevel}
                </Typography>
                {/* <Typography fontSize="0.8rem">
                      {item.course.duration?.duration ? item.course.duration?.duration : ""}{" "}
                      <sup
                        style={{
                          fontSize: "9px",
                          marginTop: "-3px",
                        }}
                      >
                        months
                      </sup>{" "}
                      |
                    </Typography>
                    <Typography fontSize="0.8rem" sx={{ display: "flex" }}>
                      <Typography
                        sx={{
                          fontSize: "13px",
                          // margin: "2px 2px 0 0",
                          position: "relative",
                          "&:hover .cancel-icon": {
                            display: "block !important",
                          },
                        }}
                      >
                        {" "}
                        {item.course.tuitionFee?.tuitionFee
                          ? item.course.currency.symbol
                          : ""}
                        <span
                          className="cancel-icon"
                          style={{
                            display: "none",
                            color: "#000",
                            position: "absolute",
                            border: "1px solid #7d7d7d",
                            width: "100px",
                            zIndex: 1,
                            background: "#fff",
                            textAlign: "center",
                            top: "0px",
                            right: 0
                          }}
                        >
                          Currency :{item.course.currency.code}
                        </span>
                      </Typography>
                      {item.course.tuitionFee?.tuitionFee
                        ? (
                          Math.round(item.course.tuitionFee.tuitionFee * 100) /
                          100
                        ).toLocaleString("en-US")
                        : ""}
                      {/* <span
                              style={{
                                fontSize: "11px",
                                marginLeft: "3px",
                                marginTop: "2px",
                              }}
                            >
                              {" "}
                              {item.course.tuitionFee?.tuitionFee
                                ? "/" +
                                  item.course.tuitionFee?.tuitionFeeType 
                                : ""}{" "}
                            </span> */}
                {/* <sup
                        style={{
                          fontSize: "9px",
                          marginLeft: "3px",
                          marginTop: "-3px",
                        }}
                      >
                        {item.course.tuitionFee?.tuitionFeeType}
                      </sup> */}
                {/* </Typography> */}
              </Stack>
            </Stack>
            {/* <p style={{
        position: 'absolute',
        right: '20px',
        top: '30%', background: "#B7B7E7", width: "1.1rem", height: "1.1rem", margin: 0, borderBottomLeftRadius: "20px", transform: "rotate(45deg"
      }}></p> */}

          </Box>
          <Grid container spacing={2} sx={{ paddingLeft: "40px", mt: "0 !important" }}>
            <Grid item xs={12} sx={{ display: "flex", alignItems: "center", justifyContent: "end", gap: "1rem", }}>
              <Box>
                {loadingMap[item.course?._id] ? (
                  <Button
                    sx={{
                      border: "1px solid #1C1F40", borderRadius: "50%", width: "2rem", display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      minWidth: 0

                    }}
                  //  sx={secondaryButton}
                  >
                    <Box
                      component="img"
                      src={Images.standardLoader}
                      alt="standardLoader"
                      sx={{ width: "1rem", height: "1rem" }}
                    />
                  </Button>
                ) : shortlisted.data.some(
                  (shortlistedCourse: any) =>
                    shortlistedCourse._id === item.course?._id
                ) ? (
                  <Button
                    sx={{
                      border: "1px solid #1C1F40", borderRadius: "50%", width: "2rem", display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      minWidth: 0
                    }}
                    // sx={[secondaryButton,{width:"100%"}]}


                    onClick={() =>
                      eventHandler({
                        name: "delete",
                        data: item,
                      })
                    }
                  >
                    <FavoriteOutlinedIcon
                      sx={{ color: "#1C1F40", fontSize: "1.2rem", }}
                    />{" "}
                    {/* Wishlisted */}
                  </Button>
                ) : (
                  <Button
                    sx={{
                      border: "1px solid #1C1F40", borderRadius: "50%", width: "2rem", height: "2rem", display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      minWidth: 0

                    }}
                    // sx={[secondaryButton,{width:"100%"}]}


                    onClick={() =>
                      eventHandler({
                        name: "shortlist",
                        data: item,
                      })
                    }
                  >
                    <FavoriteBorderIcon
                      sx={{ color: "#1C1F40", fontSize: "1.2rem", }}
                    />
                    {/* Wishlist */}
                  </Button>
                )}
              </Box>
              {/* </Grid> */}
              {/* <Grid item xs={5}> */}
              <Button
                sx={{
                  border: "1px solid #1C1F40", borderRadius: "50%", width: "2rem", height: "2rem", display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  minWidth: 0

                }}
                // sx={[globalButton,{width:"100%"}]}
                onClick={() =>
                  eventHandler({ name: "cart", data: item })
                }
              >
                <ShoppingCartOutlinedIcon sx={{ fontSize: "1.25rem", color: "#1C1F40" }} />
                {/* Add to cart */}
              </Button>
              {/* </Grid> */}
              {/* <Grid item xs={1}> */}
              <Box sx={{}}>
                {item.notInterested === true ? (
                  ""
                ) : (
                  <Tooltip
                    slotProps={{
                      tooltip: {
                        sx: {
                          color: "#fff",
                          // backgroundColor: "#3B3F76",
                          boxShadow: 1,
                          "&.MuiTooltip-tooltip": {
                            marginTop: "-5px !important",
                          },
                        },
                      },
                    }}
                    title="Not interested"
                    placement="bottom"
                  >
                    <Box
                      sx={{
                        border: "1px solid #3B3F76",
                        borderRadius: "50%",
                        p: 1,
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        width: "1rem", height: "1rem"
                        // background:"#1C1F40"
                        // width: "32%",
                        // height: "45%",
                      }}
                    >
                      <NotInterestedOutlinedIcon
                        sx={{ fontSize: "1.1rem", color: "#3b3f76" }}
                        // onClick={() =>
                        //   eventHandler({
                        //     name: "hideRecommendation",
                        //     data: item._id,
                        //   })
                        // }
                        onClick={() => handleDelete(item._id)}
                      />
                    </Box>
                  </Tooltip>
                )}
              </Box>
            </Grid>
          </Grid>
        </CustomCard>
        <ConfirmDialog
          open={open}
          handleClose={() => setOpen(false)}
          additionalData={{
            onSubmit: () =>
              eventHandler({
                name: "hideRecommendation",
                data: deleteItem,
              }),
            title: "Are you sure you want to make this course as not interested ?",
            content: "",
          }}
        />
      </div>
    );
  };

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      await new Promise((resolve) => setTimeout(resolve, 1000));
      setLoading(false);
    };

    fetchData();
  }, []);

  const filterCourses = (courses: Recommendation[]) => {
    return courses?.filter(course =>
      alignment === "interested" ? !course.notInterested : course.notInterested
    );
  };

  const tabs = [
    {
      label: `Safe (${safe && filterCourses(safe).length})`,
      Component: (
        <Box className="recommended-card-list">
          <Grid
            container
            spacing={3}
            sx={{
              // height: "100vh",
              overflowY: "scroll",
              // padding: "2rem"
            }}
          >
            {safe && filterCourses(safe).map((item, i: number) => (
              <Grid item xs={12} sm={6} lg={4}>
                <Card data={item} i={i} />
              </Grid>
            ))}
          </Grid>
        </Box>
      ),
      value: 'Safe',
    },
    {
      label: `Moderate (${moderate && filterCourses(moderate).length})`,
      Component: (
        <Box className="recommended-card-list">
          <Grid
            container
            spacing={3}
            sx={{
              // height: "100vh",
              overflowY: "scroll",
              // padding: "2rem"
            }}
          >
            {moderate && filterCourses(moderate).map((item, i) => (
              <Grid item xs={12} sm={6} lg={4}>
                <Card data={item} key={item._id} i={i} />
              </Grid>
            ))}
          </Grid>
        </Box>
      ),
      value: 'Moderate',
    },
    {
      label: `Ambitious (${ambitious && filterCourses(ambitious).length})`,
      Component: (
        <Box >
          <Grid
            container
            spacing={3}
            sx={{
              // height: "100vh",
              overflowY: "scroll",
              // padding: "2rem"
            }}
          >
            {ambitious && filterCourses(ambitious).map((item, i) => (
              <Grid item xs={12} sm={6} lg={4}>
                <Card data={item} key={item._id} i={i} />
              </Grid>

            ))}
          </Grid>
        </Box>
      ),
      value: 'Ambitious',
    },
  ]
  const dispatch = useAppDispatch();

  const regenerateForm = async () => {
    setLoading(true);
    try {
      const response = await getRecommendations({});
      if (response) {
        dispatch(setRecommended(response.data));
      } else {
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  const submit = async () => {
    if (courseChecked && underGraduation?.data?.underGraduation !== undefined && gre) {
      regenerateForm()
    } else {
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "qasection",
              dimensions: {
                width: "70%",
                height: "90%"
              },
            },
            type: "custom",
          },
        }))
    }

  }
  return (
    <>
      <Box>
        {loading ? (
          <Box sx={{
            display: "flex", flexDirection: "column",
            justifyContent: "center", alignItems: "center",
            height: "500px",
          }}>
            <Typography textAlign="center">Loading...</Typography>
          </Box>
        ) :
          recommendations?.data && recommendations?.data?.data?.length > 0 ?
            <>
              {!isCriteriaMet && (
                <Button onClick={regenerateForm} sx={secondaryButton}>
                  {loading ? (
                    <img
                      src={Images.standardLoader}
                      alt="load"
                      style={{ width: "1rem" }}
                    />
                  ) : (
                    "Regenerate"
                  )}
                </Button>
              )}
              {/* <Button onClick={()=>
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "qasection",
                    dimensions: {
                      width: "70%",
                      height: "90%"
                    },
                  },
                  type: "custom",
                },
              }))
    }>Generate</Button> */}
              <Grid container spacing={2}>
                <Grid item xs={9}>
                </Grid>
                <Grid item xs={3}>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={alignment === "interested"}
                        onChange={handleChange}
                        // color="info"
                        sx={{
                          '& .MuiSwitch-switchBase': {
                            color: '#3b3f76',
                            '&.Mui-checked': {
                              color: '#3b3f76',
                            },
                            '&.Mui-checked + .MuiSwitch-track': {
                              backgroundColor: '#3b3f76',
                            },
                          },
                          '& .MuiSwitch-track': {
                            backgroundColor: '#e0e0e0', // Adjust if needed
                          },
                        }}
                      />
                    }
                    label={alignment === "interested" ? "Interested" : "Not Interested"}
                    sx={{
                      // width: "100%",
                      ".MuiFormControlLabel-label": {
                        width: "100%",
                        textAlign: "center",
                        color: "#3b3f76",
                        fontWeight: "400",
                        textTransform: "capitalize",
                        // backgroundColor: alignment === "interested" ? "#3b3f76" : "transparent",
                        borderRadius: "8px",
                        padding: "8px",
                        transition: "background-color 0.3s",
                      },
                    }}
                  />
                </Grid>
              </Grid>
              {/* <CustomTabs tabs={tabs} /> */}
              <Box sx={{ display: "flex", gap: "1rem", mb: 2 }}>
                {tabs.map((item: any) => (
                  <Button
                    key={item.label}
                    onClick={() => setActiveTab(item.value)}
                    sx={{
                      borderBottom: item.value === activeTab ? "2px solid #3b3f76" : "none",
                      textTransform: "none",
                      borderRadius: "0",
                      color: item.value === activeTab ? "#3b3f76" : "#3b3f7699",
                      display: "flex",
                      justifyContent: "space-between",
                      p: 1,
                      px: 2,
                      "&:hover": {
                        // background: "#3b3f76",
                        color: "#3b3f76",
                      },
                    }}
                  >
                    {item.label}
                  </Button>
                ))}
              </Box>
              {tabs.find((item) => item.value === activeTab)?.Component}
            </> :
            <Box sx={{ display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", height: "500px", px: { xs: 3, md: 10 }, gap: "1rem" }}>
              <Typography sx={{ fontSize: { xs: "1.5rem", md: "2rem" }, fontWeight: "500", color: "#3B3F76" }}>AI Recommended <span style={{ color: "rgba(48, 51, 99, 0.5)" }}>Course finder</span></Typography>
              <Typography textAlign="center" sx={{ fontSize: { xs: "0.85rem", md: "1rem" } }}>Considering studying abroad but unsure about the best universities and courses for your needs? Give our AI-powered University Course Finder a try. Input your preferences, and let the AI analyze millions of data points to provide you with the information you need, saving you hours of research.</Typography>
              <CustomButton width="150px" handleSubmit={submit}>Generate</CustomButton>
            </Box>
        }

      </Box>
    </>
  );
};

export default Recommended;
