import React, { useState, useMemo } from "react";
import {
  FormControl,
  Select,
  MenuItem,
  Typography,
  TextField,
  InputAdornment,
  ListSubheader,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import SearchIcon from "@mui/icons-material/Search";

interface ReusableDropdownProps {
  label: string;
  name: string;
  options: string[];
  value: any;
  onChange: any;
  placeholder?: string;
  searchEnabled?: boolean;  // Toggle to enable or disable the search box
}

const ReusableDropdown: React.FC<ReusableDropdownProps> = ({
  label,
  name,
  options,
  value,
  onChange,
  placeholder = `Select `,
  searchEnabled = true,
}) => {
  const [searchQuery, setSearchQuery] = useState("");
// console.log(value)
  const filteredOptions = useMemo(
    () =>
      options.filter((option) =>
        option.toLowerCase().includes(searchQuery.toLowerCase())
      ),
    [options, searchQuery]
  );

  return (
    <FormControl fullWidth>
      <Select
        labelId={`${name}-label`}
        size="small"
        id={name}
        name={name}
        displayEmpty
        onClose={()=>setSearchQuery("")}
        value={value}
        onChange={onChange}
        fullWidth
        IconComponent={ExpandMoreIcon}
        renderValue={(selected) => {
          if (!selected) {
            return (
              <Typography sx={{ color: "#757575", fontSize: "14px" }}>
                {placeholder}
              </Typography>
            );
          }
          return selected;
        }}
        MenuProps={{
          PaperProps: {
            sx: {
              maxHeight: 300,
              maxWidth: 300,
              "&.MuiPaper-root": {
                boxShadow: 3,
                borderRadius: "15px !important",
              },
            },
          },
          autoFocus: false,
        }}
        sx={{
        }}
      >
        {searchEnabled && (
          <ListSubheader>
            <TextField
              size="small"
              autoFocus
              placeholder="Type to search..."
              fullWidth
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
              onKeyDown={(e) => {
                if (e.key !== "Escape") {
                  e.stopPropagation();
                }
              }}
            />
          </ListSubheader>
        )}
        <MenuItem
          disabled
          value=""
          sx={{
            "& .MuiTypography-root": {
              fontSize: "14px !important",
            },
          }}
        >
          {placeholder} 
        </MenuItem>
        {filteredOptions.length > 0 ? (
          filteredOptions.map((name, i) => (
            <MenuItem
              key={i}
              value={name}
              sx={{
                "& .MuiTypography-root": {
                  fontSize: "14px !important",
                },
                "& .MuiSvgIcon-root ": {
                  fontSize: "1rem",
                },
                whiteSpace: "normal",
              }}
            >
              {name}
            </MenuItem>
          ))
        ) : (
          <MenuItem disabled>No options available</MenuItem>
        )}
      </Select>
    </FormControl>
  );
};

export default ReusableDropdown;
