// import { Cancel } from "@mui/icons-material";
import {
  Autocomplete,
  Box,
  Button,
  // Button,
  FormControl,
  Grid,
  InputAdornment,
  InputLabel,
  MenuItem,
  Select,
  Slider,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import { Form, Formik } from "formik";
import React, { useEffect, useState } from "react";
import Images from "../../../../assets";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import {
  countryList,
  currencySymbols,
  degreeType,
  monthNames,
  skillsList,
  subDisciplines,
} from "../../../../assets/menu";
import { editProfile, requestAdvisor } from "../../../../services";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { setpreferences } from "../../../../store/Slices/preferencesSlice";
import { setskills } from "../../../../store/Slices/skillsSlice";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { Advisor, Event } from "../../../../types/types";
import { CustomButton } from "../../../../genericComponents/customButton";
import * as Yup from "yup";
// import { useNavigate } from "react-router-dom";
import { addAdvisorInfo, updateAdvisorInfo } from "src/store/Slices/advisorsSlice";
import { addChats } from "src/store/Slices/chatsSlice";
import { secondaryButton } from "src/components/styles/customStyles";
// interface CurrencyOption {
//   label: string;
//   value: string;
// }
const Preferences = () => {
  const Preferences = useAppSelector((state) => state.preferences);
  const dispatch = useAppDispatch();
  const [loading, setLoading] = useState(false);
  const [intake, setIntake] = useState<any>(Preferences.data?.intake)
  const date = new Date(intake)
  // const monthDate = date.getMonth()
  const [year, setYear] = useState(Preferences.data?.intake ? date.getFullYear() : "")
  const [month, setMonth] = useState<any>(Preferences.data?.intake ? date.getMonth() : "")
  const [courseData, setCourseData] = useState(
    Preferences?.data?.courses || []
  );
  const [countryData, setCountryData] = useState(
    Preferences?.data?.country || []
  );
  const [minBudget, setMinBudget] = useState<number>(
    Preferences?.data?.budget?.lower || 0
  );
  const [maxBudget, setMaxBudget] = useState<number>(
    Preferences?.data?.budget?.upper || 5000000
  );
  const [selectedCurrency, setSelectedCurrency] = useState<any>(
    Preferences?.data?.currency
  );

  const advisors = useAppSelector((state) => state.advisor);
  // const navigate = useNavigate();
  const assignedCountries =
    advisors?.data?.flatMap((advisor: Advisor) => advisor.assignedCountries) ||
    [];
  const unassignedCountries = Preferences?.data?.country?.filter(
    (country: string) => !assignedCountries.includes(country)
  );
  // eslint-disable-next-line
  const validationSchema = Yup.object().shape({
    preference: Yup.object().shape({
      intake: Yup.string().required("Intake is required"),
      budget: Yup.object().shape({
        lower: Yup.number().required("Lower budget is required"),
        upper: Yup.number()
          .min(Yup.ref("lower"), "Upper budget must be greater than lower budget")
          .required("Upper budget is required"),
      }),
      degree: Yup.string().required("Degree is required"),
      country: Yup.array()
        .of(Yup.string())
        .min(1, "At least one country is required"),
      courses: Yup.array()
        .of(Yup.string())
        .min(1, "At least one course is required"),
      currency: Yup.string().required("Currency is required"),
    }),

  });

  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 150,
      },
    },
  };

  const currencies = currencySymbols.map((symbol) => {
    // eslint-disable-next-line
    const [label, value] = symbol.split("-");
    return { label: symbol, value };
  });

  // const handleOptionChange = (
  //   event: React.ChangeEvent<{}>,
  //   newValue: string[]
  // ) => {
  //   setSkillsData(newValue);
  // };

  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "OptionChange":
        setSelectedCurrency(event.data.newValue?.value);
        break;
      case "sliderChange":
        if (Array.isArray(event.data.newValue)) {
          setMinBudget(event.data.newValue[0]);
          setMaxBudget(event.data.newValue[1]);
        }
        break;
      case "countryChange":
        setCountryData(event.data.newValue);
        break;
      case "courseChange":
        setCourseData(event.data.newValue);
        break;
    }
  };
  useEffect(() => {
    if (month !== undefined && year !== undefined) {
      setIntake(monthNames[month] + "-" + year);
    }
    // eslint-disable-next-line
  }, [month, year]);


  const initialValues = {
    preference: {
      intake: intake,
      budget: {
        upper: maxBudget,
        lower: minBudget,
      },
      degree: Preferences?.data?.degree,
      country: countryData,
      courses: courseData,
      currency: selectedCurrency,
    },
    // skills: skilldata,
  };

  const submit = async (values: any) => {
    setLoading(true);
    try {
      const response = await editProfile(values);
      if (response) {
        dispatch(setpreferences(response.data.data.preference));
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "success",
                data: "Preferences updated ",
                dimensions: {
                  width: "30%",
                  height: "40%",
                  show: "none"

                },
              },
              type: "custom",
            },
          })
        );
        setLoading(false);
      }
    } catch (error) {
      setLoading(false);
    }
  };

  const addAdvisors = async (item: string) => {
    try {
      const response = await requestAdvisor({
        country: item,
      });

      const newAdvisor = response.data.data.advisor;
      const existingAdvisor = advisors?.data?.find((advisor) => advisor._id === newAdvisor._id);

      if (existingAdvisor) {
        dispatch(updateAdvisorInfo(newAdvisor));
      } else {
        dispatch(addAdvisorInfo(newAdvisor));
      }
      if (response.data.data.chat !== null) {
        dispatch(addChats(response.data.data.chat));
      }
    } catch (err) {
      console.error(err);
    }
  };
  return (
    <Box sx={{ px:{lg: 3} }}>
      <Formik
        initialValues={initialValues}
        enableReinitialize={true}
        // validationSchema={validationSchema}
        onSubmit={(values: any) => submit(values)}
      >
        {({ touched, errors, values, handleChange, setFieldValue }) => (
          <Form className="mt-5">
            <Grid container spacing={4} sx={{ width:"100% !important" }}>
              <Grid
                item
                xs={12}
                md={6}
                sx={{ display: "flex", flexDirection: "column" }}
              >
                <Typography fontWeight="500" gutterBottom sx={{}}
                >
                  What program would you like to pursue?
                </Typography>
                <Autocomplete
                  multiple
                  id="subdisciplines"
                  sx={{
                    "& .MuiOutlinedInput-root .MuiAutocomplete-input": {
                      padding: "5px",
                    },
                    width: "100%",
                  }}
                  limitTags={2}
                  size="small"
                  options={subDisciplines}
                  getOptionLabel={(option) => option}
                  value={courseData}
                  filterSelectedOptions
                  ListboxProps={{
                    style: { maxHeight: "180px", overflowY: "auto" },
                  }}
                  onChange={(event, newValue) =>
                    eventHandler({
                      name: "courseChange",
                      data: { event, newValue },
                    })
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      placeholder="Search programs"
                      variant="outlined"
                      InputProps={{
                        ...params.InputProps,
                      }}
                    />
                  )}
                />
                {touched.preference?.courses && errors.preference?.courses ? (
                  <div className="error">{errors.preference.courses}</div>
                ) : null}
              </Grid>
              <Grid
                item
                xs={12}
                md={6}
                sx={{ display: "flex", flexDirection: "column" }}
              >
               <Typography fontWeight="500" gutterBottom sx={{}}
                >
                  What are your preferred study destinations?
                </Typography>
                <Autocomplete
                  multiple
                  id="autocomplete"
                  sx={{
                    "& .MuiOutlinedInput-root .MuiAutocomplete-input": {
                      padding: "5px",
                    },
                    width: "100%",
                  }}
                  limitTags={2}
                  size="small"
                  options={countryList}
                  getOptionLabel={(option) => option}
                  value={countryData}
                  filterSelectedOptions
                  ListboxProps={{
                    style: { maxHeight: "180px", overflowY: "auto" },
                  }}
                  onChange={(event, newValue) =>
                    eventHandler({
                      name: "countryChange",
                      data: { event, newValue },
                    })
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      placeholder="Search country"
                      variant="outlined"
                      InputProps={{
                        ...params.InputProps,
                      }}
                    />
                  )}
                />
                {touched.preference?.country && errors.preference?.country ? (
                  <div className="error">{errors.preference.country}</div>
                ) : null}
                {unassignedCountries?.map((item, i) => (
                  <Grid item xs={12} key={i}>
                    <Box
                      sx={{
                        py: 1,
                        display: "flex",
                        gap: "1rem",
                        alignItems: "center",
                      }}
                    >
                      <Button
                        onClick={() => addAdvisors(item)}
                        sx={{
                          border: "1px solid #3B3F76CC",
                          borderRadius: "15px",
                          p: 0.5,
                          textTransform: "none",
                          color: "#3B3F76CC",
                        }}
                      >
                        Request advisor for {item}
                      </Button>
                    </Box>
                  </Grid>
                ))}
              </Grid>
              <Grid
                item
                xs={12}
                md={6}
                sx={{ display: "flex", flexDirection: "column" }}
              >
               <Typography fontWeight="500" gutterBottom sx={{}}
                >
                  Which degree program do you prefer?
                </Typography>
                <FormControl size="small">
                  <InputLabel id="degree">Select Degree</InputLabel>
                  <Select
                    labelId="degree"
                    id="degree"
                    size="small"
                    name="preference.degree"
                    MenuProps={MenuProps}
                    value={values.preference.degree}
                    label="Select Degree"
                    onChange={handleChange}
                  >
                    {degreeType.map((item, i) => (
                      <MenuItem value={item} key={i}>
                        {item}
                      </MenuItem>
                    ))}
                  </Select>
                  {touched.preference?.degree && errors.preference?.degree ? (
                    <div className="error">{errors.preference.degree}</div>
                  ) : null}
                </FormControl>
              </Grid>
              <Grid item xs={12} md={6} >
                <Typography fontWeight="500" gutterBottom sx={{}}
                >
                  When would you like to begin your studies?
                </Typography>
                <div style={{ display: "flex", gap: "1rem" }}>
                  <Select
                    labelId="degree"
                    id="degree"
                    size="small"
                    // name="preference.degree"
                    MenuProps={MenuProps}
                    value={year}
                    // label="Select Year"
                    onChange={(e: any) => setYear(e.target.value)}
                    defaultValue=""
                    displayEmpty
                  >
                    <MenuItem value="" disabled>Select Year</MenuItem>
                    {["2025", "2026", "2027", "2028", "2029", "2030"].map((item, i) => (
                      <MenuItem value={item} key={i}>
                        {item}
                      </MenuItem>
                    ))}
                  </Select>
                  <Select
                    labelId="degree"
                    id="degree"
                    size="small"
                    // name="preference.degree"
                    MenuProps={MenuProps}
                    value={month}
                    // label="Select Year"
                    onChange={(e: any) => setMonth(e.target.value)}
                    defaultValue=""
                    displayEmpty
                  >
                    <MenuItem value="" disabled>Select Month</MenuItem>
                    {monthNames.map((item, i) => (
                      <MenuItem value={i} key={i}>
                        {item}
                      </MenuItem>
                    ))}
                  </Select>
                </div>

                {/* {touched.preference?.intake && errors.preference?.intake ? (
                  <div className="error">{errors.preference.intake}</div>
                ) : null} */}
              </Grid>
              <Grid item xs={12} md={6}>
                <Typography fontWeight="500" gutterBottom sx={{ mb: 0.5 }}>
                  What is your estimated budget range for your studies?
                </Typography>
                <>
                  <Stack direction="row" spacing={1} sx={{ mt: 1 }}>
                    <TextField
                      name="minBudget"
                      label="Min Budget"
                      type="number"
                      size="small"
                      value={minBudget}
                      onChange={(e: any) =>
                        setMinBudget(Number(e.target.value))
                      }
                      InputProps={{
                        sx: {
                          "input[type=number]": {
                            "-moz-appearance": "textfield", // Firefox
                            "-webkit-appearance": "none", // Chrome, Safari, Edge
                            margin: 0, // Remove margin in Safari
                          },
                          "input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button":
                          {
                            "-webkit-appearance": "none",
                          },
                        },
                        startAdornment: (
                          <InputAdornment position="start">
                            {values.preference.currency}
                          </InputAdornment>
                        ),
                      }}
                    />
                    <TextField
                      name="maxBudget"
                      label="Max Budget"
                      type="number"
                      size="small"
                      value={maxBudget}
                      onChange={(e: any) =>
                        setMaxBudget(Number(e.target.value))
                      }
                      InputProps={{
                        sx: {
                          "input[type=number]": {
                            "-moz-appearance": "textfield", // Firefox
                            "-webkit-appearance": "none", // Chrome, Safari, Edge
                            margin: 0,
                          },
                          "input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button":
                          {
                            "-webkit-appearance": "none",
                          },
                        },
                        startAdornment: (
                          <InputAdornment position="start">
                            {values.preference.currency}
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Stack>
                  <Slider
                    value={[minBudget, maxBudget]}
                    onChange={(event: any, newValue: number | number[]) =>
                      eventHandler({
                        name: "sliderChange",
                        data: { event, newValue },
                      })
                    }
                    min={0}
                    max={5000000}
                    step={1}
                    valueLabelDisplay="auto"
                    sx={{
                      mt: 2,
                      width: { xs: "100%", md: "50%" },
                      color: "#979a9d",
                      height: "2px",
                    }}
                  />
                  {touched.preference?.budget?.lower &&
                    errors.preference?.budget?.lower ? (
                    <div className="error">
                      {errors.preference.budget.lower}
                    </div>
                  ) : null}
                  {touched.preference?.budget?.upper &&
                    errors.preference?.budget?.upper ? (
                    <div className="error">
                      {errors.preference.budget.upper}
                    </div>
                  ) : null}
                </>
              </Grid>
              <Grid item xs={12} md={6}>
                <InputLabel
                  id="degree"
                  sx={{ fontWeight: "500", color: "#000", mb: 0.5 }}
                >
                  Preferred currency?
                </InputLabel>
                <Autocomplete
                  sx={{
                    marginTop: "9px",
                    width: "100%",
                    "& .MuiInputBase-input": {
                      fontSize: "14px !important",
                    },
                    "&.MuiAutocomplete-listbox": {
                      width: "400px",
                    },
                    "&.MuiAutocomplete-listbox .MuiAutocomplete-option ": {
                      fontSize: "14px !important",
                    },
                    "& .MuiOutlinedInput-root": {
                      paddingRight: "0 !important",
                    },
                  }}
                  options={currencies}
                  getOptionLabel={(option) => option?.label}
                  value={
                    currencies.find(
                      (option) => option.value === selectedCurrency
                    ) || null
                  }
                  onChange={(
                    event: any,
                    newValue: { label: string; value: string } | null
                  ) =>
                    eventHandler({
                      name: "OptionChange",
                      data: { event, newValue },
                    })
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      placeholder="Search currency"
                      size="small"
                      InputProps={{
                        ...params.InputProps,
                        type: "search",
                        endAdornment: (
                          <ExpandMoreIcon
                            sx={{ color: "gray", marginRight: "5px" }}
                          />
                        ),
                        style: {
                          fontSize: "14px !important",
                          color: "#000 !important",
                        },
                      }}
                    />
                  )}
                />
                {touched.preference?.currency && errors.preference?.currency ? (
                  <div className="error">
                    {String(errors.preference.currency)}
                  </div>
                ) : null}
              </Grid>


              <Grid
                item
                xs={12}
                sx={{ justifyContent: "end", display: "flex" }}
              >
                <CustomButton
                  width="100px"
                  handleSubmit={() => console.log("first")}
                >
                  {loading ? (
                    <img
                      src={Images.standardLoader}
                      alt="load"
                      style={{ width: "1rem" }}
                    />
                  ) : (
                    "Save"
                  )}
                </CustomButton>
              </Grid>
            </Grid>
          </Form>
        )}
      </Formik>

    </Box>
  );
};

