import { Box, Grid,Tooltip, Typography } from "@mui/material";
import React, { useState } from "react";
import Images from "../../../../assets";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import CustomCard from "../../../../genericComponents/customCard";
import ConfirmDialog from "../../../../genericComponents/modalPopup/confirmDialog";
import { addToShortList} from "../../../../services";
import { setPopup } from "../../../../store/Slices/popupSlice";
import {  setShortlisted } from "../../../../store/Slices/shortlistedCoursesSlice";
import {  Event, ShortlistedCourse } from "../../../../types/types";
import TimerIcon from "@mui/icons-material/Timer";
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import { monthNames } from "../../../../assets/menu";

const Shortlisted = () => {
  const shortlistedCourses = useAppSelector(
    (state) => state.shortlistedcourses
  );
  const [open, setOpen] = useState(false);
  const [deleteId, setDeleteId] = useState("");
  const userAuthStatus = useAppSelector((state) => state.userAuthStatus);
  const dispatch = useAppDispatch();
  // const navigate = useNavigate();
  const handleDeleteOpen = (id: string) => {
    setOpen(true);
    setDeleteId(id);
  };
  const handleDelete = async () => {
    const values = {
      action: "pull",
      courseId: deleteId,
    };
   
    const response = await addToShortList(values);
    if (response.data.success) {
      dispatch(setShortlisted(response.data.data))
     
    }
      setOpen(false);
    
  };
  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "cart":
        try {
          if (
            userAuthStatus?.data?.isAuthorized &&
            userAuthStatus?.data?.isRegistered
          ) {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "applynow",
                    data:event.data,
                    dimensions: {
                      width: "450px",
                    },
                  },
                  type: "custom",
                },
              })
            );
          } else {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "login",
                    dimensions: {
                      width: "800px",
                  height:"70%",
                  show:"none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } catch (error) {}
        break;
    }
  };
  // const courseMonths = shortlistedCourses?.data?.startDate
  // ? monthNames[shortlistedCourses?.data?.startDate?.[0]?.courseStartingMonth]
  // : "No course starting month";

  // const shortlistedCoursesData = shortlistedCourses.data?.map((item) => ({
  //   name: item.course.name,
  //   value: item.course._id
  // }));
  return (
    <div>
      <Grid
        container
        spacing={3}
        sx={{
          // maxHeight: "81vh",
          overflowY: "scroll",
          
        }}
      >
        {shortlistedCourses.data && shortlistedCourses?.data?.length > 0 ? (
          <>
            {shortlistedCourses.data?.map(
              (item: ShortlistedCourse, i: number) => (
                <Grid item xs={12} sm={6} lg={4} sx={{paddingLeft:{xs:0,md:"1rem"}}}>
                  <CustomCard title={item?.university?.location?.country} i={i}>
                    <Box
                      onClick={() => {
                        dispatch(
                          setPopup({
                            show: true,
                            data: {
                              container: {
                                name: "mainPopup",
                                data: item,
                                dimensions: {
                                  width: "80%",
                                  // height:"80%"
                                },
                              },
                              type: "custom",
                            },
                          })
                        );
                      }}
                    >
                      <Box sx={{ display: "flex", gap: "10px",}}>
                        <img
                          src={item.university.logoSrc}
                          alt="logo"
                          style={{
                            width: "1.5rem",
                            height: "1.5rem",
                            // marginRight: "5px",
                            borderRadius: "10px",
                          }}
                        />
                        
                        <div style={{ flex: 1 }}>
                        <Typography
                              fontSize="0.9rem"
                              sx={{
                                fontSize: { xs: "13px", lg: "14px" },
                                fontWeight: 500,
                                textOverflow: "ellipsis ",
                                lineClamp: 1,
                                height: "1.5rem",
                                textTransform: "capitalize",
                                overflow: "hidden",
                                display: "-webkit-box",
                                WebkitLineClamp: "1",
                                WebkitBoxOrient: "vertical",
                                mb:1
                              }}
                            >
                              {item?.university?.name}
                            </Typography>
                          <Tooltip
                            slotProps={{
                              tooltip: {
                                sx: {
                                  color: "#fff",
                                  backgroundColor: "#3B3F76",
                                  boxShadow: 1,
                                  "&.MuiTooltip-tooltip": {
                                    marginTop: "-5px !important",
                                  },
                                },
                              },
                            }}
                            title={item.name}
                            placement="bottom"
                          >
                            <Typography
                              fontSize="0.9rem"
                              sx={{
                                fontSize: { xs: "13px", lg: "14px" },
                                fontWeight: 500,
                                textOverflow: "ellipsis ",
                                lineClamp: 1,
                                height: "1.5rem",
                                textTransform: "capitalize",
                                overflow: "hidden",
                                display: "-webkit-box",
                                WebkitLineClamp: "1",
                                WebkitBoxOrient: "vertical",
                              }}
                            >
                              {item.name}
                            </Typography>
                          </Tooltip>
                          
                          
                          <Typography
                            component="span"
                            fontSize="0.8rem"
                            fontWeight="500"
                            sx={{
                              display: "flex",
                              alignItems: "center",
                              whiteSpace: "nowrap",
                              mt: 1,
                            }} // Prevent line wrapping
                          >
                            <TimerIcon sx={{ fontSize: "1.25rem" }} />
                            &nbsp; Start date :
                            {item?.startDate
                              ?.map(
                                (item: any, index: number) =>
                                  monthNames[item?.courseStartingMonth]
                              )
                              .join(", ")}
                            {/* {item?.intake ? date(new Date(item?.courseMonths)) : null} */}
                          </Typography>
                        </div>
                      </Box>
                    </Box>
                    <Grid container spacing={2} >
                      <Grid
                        item
                        xs={10.5}
                        sx={{
                          display: "flex",
                          justifyContent: "end",
                          alignItems: "center",
                        }}
                      >
                        <Box
                          sx={{border:"1px solid #1C1F40",borderRadius:"50%",width:"2rem",height:"2rem",display: "flex",
                          justifyContent: "center",
                          alignItems: "center",}}
                          onClick={() =>
                            eventHandler({ name: "cart", data: item })
                          }
                        >
                         <ShoppingCartOutlinedIcon sx={{fontSize:"1.25rem",color:"#1C1F40"}} />
                        </Box>
                      </Grid>
                      <Grid item xs={1.5}>
                        <Box
                          component="img"
                          src={Images.deleteIcon}
                          alt="icon"
                          sx={{width:"2rem",height:"2rem"}}
                          onClick={() => handleDeleteOpen(item._id)}
                        />
                      </Grid>
                    </Grid>
                    {/* <p style={{
        position: 'absolute',
        left: '10px',
        bottom: '10%', background: "#B7B7E7", width: "1.1rem", height: "1.1rem", margin: 0, borderBottomLeftRadius: "20px", transform: "rotate(45deg"
      }}></p> */}
                  </CustomCard>
                </Grid>
              )
            )}
          </>
        ) : (
          <Grid
            item
            xs={12}
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "200px",
              flexDirection: "column",
            }}
          >
            <img
              src={Images.noData}
              alt="noData"
              style={{ width: "70px", height: "70px" }}
            />
            <Typography>No wishlisted courses</Typography>
          </Grid>
        )}
      </Grid>
      <ConfirmDialog
        open={open}
        handleClose={() => setOpen(false)}
        additionalData={{
          onSubmit: handleDelete, // Pass the function directly without curly braces
          title: "Are you sure you want to delete this from your shortlist?",
          content:
            "Youre just a few steps away from applying to universities.",
        }}
      />
    </div>
  );
};

export default Shortlisted;
