import { Box, Button, Grid, Tooltip, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import Images from "../../../assets";
import { useAppDispatch, useAppSelector } from "../../../assets/hooks";
import {
  globalButton, secondaryButton,
} from "../../../components/styles/customStyles";
import CustomCard from "../../../genericComponents/customCard";
import TimerIcon from "@mui/icons-material/Timer";
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { Event } from "../../../types/types";
import { setPopup } from "../../../store/Slices/popupSlice";
import { updateCart } from "../../../services";
import { setCart } from "../../../store/Slices/cartSlice";
import ConfirmDialog from "../../../genericComponents/modalPopup/confirmDialog";
import { useNavigate } from "react-router-dom";


interface CourseItem {
  _id: string;
  course: {
    _id: string;
    name: string;
    elite: boolean;
    university: {
      name: string;
      logoSrc: string;
      location: {
        country: string;
      };
    };
  };
  intake: string;
}


const Shortlisted: React.FC = () => {
  const advisors = useAppSelector((state: any) => state.advisor);
  const cartCourses = useAppSelector((state: any) => state.cart);
  const orders = useAppSelector((state: any) => state.orders);
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  // const preference= useAppSelector(store =>store.preferences)
  const [deleteItem, setDeleteItem] = useState<CourseItem | null>(null);
  const [open, setOpen] = useState<boolean>(false);
  const [selectedCourses, setSelectedCourses] = useState<string[]>([]);
  const [isProceedDisabled, setIsProceedDisabled] = useState<boolean>(true);
  // const countryPreference = preference?.data?.country?.map((item) => ({
  //   name: item,
  //   value: item
  // }));

  useEffect(() => {
    const hasSelectedCourses = selectedCourses.length > 0;
    const hasAlreadyApplied = selectedCourses.some((courseId) => {
      const course = cartCourses.data.find((item: CourseItem) => item._id === courseId);
      return course ? isAlreadyApplied(course) : false;
    });
    setIsProceedDisabled(!hasSelectedCourses || hasAlreadyApplied);
    // eslint-disable-next-line
  }, [selectedCourses, cartCourses]);

  const isAlreadyApplied = (course: CourseItem): boolean => {
    return orders?.data?.some(
      (order: any) =>
        order.products?.some(
          (product: any) =>
            product?.course?._id === course?.course?._id &&
            new Date(product?.intake).getTime() === new Date(course?.intake).getTime()
        )
    );
  };
  const premiumApplicationCount = orders.data.reduce((acc: number, order: any) => {
    const premiumProducts = order.products?.filter((product: any) => product.category === "premium application") || [];
    return acc + premiumProducts.length;
  }, 0);
  
  // console.log(premiumApplicationCount);

  const handleCardClick = (item: CourseItem) => {
    if (selectedCourses.includes(item._id)) {
      setSelectedCourses(selectedCourses.filter((id) => id !== item._id));
    } else {
      setSelectedCourses([...selectedCourses, item._id]);
    }
  };

  const handleDeleteOpen = (item: CourseItem) => {
    setOpen(true);
    setDeleteItem(item);
  };

  const handleRequestQuote = () => {
    if (advisors?.data?.length > 0) {
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "advisor",
              data: advisors?.data?.[0],
              dimensions: {
                width: "900px",
                height: "85%"
              },
            },
            type: "custom",
          },
        })
      );
    } else {
      navigate("/profile/advisors");
    }
  };

  const handleCheckout = () => {
    const selectedCourseData = cartCourses.data.filter((item: CourseItem) =>
      selectedCourses.includes(item._id)
    );
    dispatch(
      setPopup({
        show: true,
        data: {
          container: {
            name: "checkout",
            data: selectedCourseData,
            dimensions: {
              width: "90%",
              height: "90%",
            },
          },
          type: "custom",
        }
      }))
  }

  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "cart":
        try {
          let values = {
            action: "remove",
            category: event?.data?.course?.elite ? "elite application" : "premium application",
            courseId: event?.data?.course._id,
            intake: event?.data?.intake,
            itemId: event?.data?._id,
          };
          const response = await updateCart(values);
          if (response.data.success) {
            dispatch(setCart(response.data.data))
          } else {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "success",
                    data: response.data.message,
                    dimensions: {
                      width: "30%",
                      height: "40%",
                      show: "none",
                      // height:"20%"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } catch (error) { }
        setOpen(false);
        break;
      // case "checkout":
      //   dispatch(
      //     setPopup({
      //       show: true,
      //       data: {
      //         container: {
      //           name: "checkout",
      //           data: courseCards,
      //           dimensions: {
      //             width: "90%",
      //             height:"90%"
      //           },
      //         },
      //         type: "custom",
      //       },
      //     })
      //   );
      // break;
      default:
        break;
    }
  };

  const formatDate = (dateString: string): string => {
    const date = new Date(dateString);
    const month = date.toLocaleString("default", { month: "short" });
    const year = date.getFullYear();
    return `${month} ${year}`;
  };


  return (
    <Box>
      <Box sx={{ height: "65vh", overflowY: "scroll", p: 1 }}>
        <Grid container spacing={3}>
          {cartCourses.data && cartCourses.data.length > 0 ? (
            cartCourses.data.map((item: CourseItem, i: number) => {
              const alreadyApplied = isAlreadyApplied(item);
              const isSelected = selectedCourses.includes(item._id);
              return (
                <Grid
                  item
                  xs={12}
                  sm={6}
                  lg={4}
                  key={item._id}
                  sx={{ paddingLeft: { xs: 0, md: "1rem" } }}
                >
                  <CustomCard
                    title={item?.course?.university?.location?.country}
                    disabled={alreadyApplied}
                    i={i}
                  >
                    <Box sx={{ display: "flex", gap: "10px" }}
                     onClick={() => {
                      dispatch(
                        setPopup({
                          show: true,
                          data: {
                            container: {
                              name: "mainPopup",
                              data: item.course,
                              dimensions: {
                                width: "80%",
                                // height:"80%"
                              },
                            },
                            type: "custom",
                          },
                        })
                      );
                    }}
                    >
                      <img
                        src={item.course.university.logoSrc}
                        alt="logo"
                        style={{
                          width: "1.5rem",
                          height: "1.5rem",
                          borderRadius: "10px",
                        }}
                      />
                      <Box sx={{ flex: 1 }}>
                        <Typography
                          fontSize="0.9rem"
                          fontWeight="500"
                          gutterBottom
                          sx={{
                            textOverflow: "ellipsis",
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            mb: 1.5,
                            textTransform: "capitalize",
                          }}
                        >
                          {item.course.university.name}
                        </Typography>
                        <Tooltip
                          title={item.course.name}
                          placement="bottom"
                          slotProps={{
                            tooltip: {
                              sx: {
                                color: "#fff",
                                backgroundColor: "#3B3F76",
                                boxShadow: 1,
                                "&.MuiTooltip-tooltip": {
                                  marginTop: "-5px !important",
                                },
                              },
                            },
                          }}
                        >
                          <Typography
                            fontSize="0.9rem"
                            sx={{
                              fontSize: { xs: "13px", lg: "14px" },
                              fontWeight: 500,
                              textOverflow: "ellipsis ",
                              lineClamp: 1,
                              height: "1.5rem",
                              textTransform: "capitalize",
                              overflow: "hidden",
                              display: "-webkit-box",
                              WebkitLineClamp: "1",
                              WebkitBoxOrient: "vertical",
                            }}
                          >
                            {item.course.name}
                          </Typography>
                        </Tooltip>
                        <Typography
                          component="span"
                          fontSize="0.8rem"
                          fontWeight="500"
                          sx={{
                            display: "flex",
                            alignItems: "center",
                            whiteSpace: "nowrap",
                            mt: 1,
                          }}
                        >
                          <TimerIcon sx={{ fontSize: "1.25rem" }} />
                          &nbsp; Start date:{" "}
                          {item.intake ? formatDate(item.intake) : "N/A"}
                        </Typography>
                      </Box>
                    </Box>
                    <Grid
                      container
                      spacing={2}
                      sx={{ mt: 1, alignItems: "center" }}
                    >
                      <Grid
                        item
                        xs={6}
                        sx={{
                          display: "flex",
                          justifyContent: "center",
                        }}
                      >
                        {!item.course.elite && !alreadyApplied && premiumApplicationCount < 5 && (
                          <Button
                            sx={{
                              // width: "100%",
                              color: "#fff",
                              background:
                                "linear-gradient(90deg, #0252B2 0%, #561D6C 100%)",
                              fontWeight: "500",
                              fontSize: "10px",
                              textTransform: "none",
                              borderRadius: "15px",
                              padding: "3px 0",
                              width: "100px"
                            }}
                          >
                            Apply for free
                          </Button>
                        )}
                      </Grid>
                      <Grid
                        item
                        xs={6}
                        sx={{
                          display: "flex",
                          justifyContent: "flex-end",
                          alignItems: "center",
                          gap: "8px",
                        }}
                      >
                        {alreadyApplied ? (
                          <>
                            <img
                              src={Images.deleteIcon}
                              alt="delete"
                              style={{ width: "1.5rem", height: "1.5rem" }}
                              onClick={() => handleDeleteOpen(item)}
                            />
                            <CheckCircleIcon
                              sx={{
                                fontSize: "1.25rem",
                                color: "#02b524",
                              }}
                            />
                            <Typography
                              sx={{
                                fontSize: "12px",
                                color: "#02b524",
                                textTransform: "capitalize",
                                whiteSpace: "nowrap",
                              }}
                            >
                              Already placed
                            </Typography>
                          </>
                        ) : (
                          <>
                            <img
                              src={Images.deleteIcon}
                              alt="delete"
                              style={{ width: "1.5rem", height: "1.5rem" }}
                              onClick={() => handleDeleteOpen(item)}
                            />
                            {isSelected ? (
                              <CheckCircleIcon
                                sx={{
                                  fontSize: "1.35rem",
                                  color: "#1C1F40",
                                }}
                                onClick={() => handleCardClick(item)}
                              />
                            ) : (
                              <Box
                                sx={{
                                  width: '1.1rem',
                                  height: '1.1rem',
                                  borderRadius: '50%',
                                  border: '1px solid #000',
                                  marginTop: '3px',
                                  marginLeft: "5px"
                                }}
                                onClick={() =>
                                  handleCardClick(item)
                                }
                              ></Box>
                            )}
                          </>
                        )}
                      </Grid>
                    </Grid>
                  </CustomCard>
                </Grid>
              );
            })
          ) : (
            <Grid
              item
              xs={12}
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "200px",
                flexDirection: "column",
              }}
            >
              <img
                src={Images.noData}
                alt="noData"
                style={{ width: "70px", height: "70px", marginBottom: "1rem" }}
              />
              <Typography sx={{ mb: 2 }}>Your cart is empty</Typography>
              <Button sx={[globalButton, { width: "150px" }]}
                onClick={() => {
                  const subDisUrl = `activeTab=${encodeURIComponent(JSON.stringify('courses'))}&FilterData=${encodeURIComponent(JSON.stringify([]))}&page=${JSON.stringify(
                    1
                  )}&search=${encodeURIComponent(JSON.stringify(''))}`
                  const subDisciUrl = window.btoa(subDisUrl);
                  navigate(`/explore?${subDisciUrl}`);
                }}
              >Add products</Button>
            </Grid>
          )}
        </Grid>
      </Box>

      {cartCourses.data && cartCourses.data.length > 0 && (
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
            flexDirection: { xs: "column", sm: "row" },
            gap: { xs: 2, md: 2 },
            mb: 2,
            p: 1,
          }}
        >
          <Button
            sx={[secondaryButton, { width: "200px" }]}
            onClick={handleRequestQuote}
          >
            Request a Quote
          </Button>


          {isProceedDisabled ? (
            <Tooltip
              slotProps={{
                tooltip: {
                  sx: {
                    color: "#fff",
                    backgroundColor: "#3B3F76",
                    boxShadow: 1,
                    "&.MuiTooltip-tooltip": {
                      marginTop: "-5px !important",
                    },
                  },
                },
              }}
              title="Select courses to proceed"
              placement="top"
            >
              <span style={{cursor:"pointer"}}>
                <Button
                  sx={[globalButton, { width: "200px", background: "#dfe1e6" }]}
                  disabled
                >
                  Proceed to Checkout
                </Button>
              </span>
            </Tooltip>
          ) : (
            <Button
              sx={[globalButton, { width: "200px", background: "#3b3f76" }]}
              onClick={handleCheckout}
            >
              Proceed to Checkout
            </Button>
          )}

        </Box>
      )}
      <ConfirmDialog
        open={open}
        handleClose={() => setOpen(false)}
        additionalData={{
          onSubmit: () => eventHandler({ name: "cart", data: deleteItem }),
          title: "Are you sure you want to delete this from your cart?",
          content:
            "Youre just a few steps away from applying to universities.",
        }}
      />

    </Box>
  );
};



const Cart = () => {
  return (
    <div  >
      <Box >
        <Typography color="#3B3F76" sx={{ fontSize: "1.2rem", fontWeight: "500", mb: 2 }}>Products</Typography>
        <Box >
          <Shortlisted />

        </Box>
      </Box>

    </div>
  )
}

export default Cart


