import { Box, Button, Grid, Stack, Tooltip, Typography } from "@mui/material";
import React, { useRef, useState } from "react";
import Images from "../../../../assets";
// import CircularWithValueLabel from "./circularprogress";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";
import InfoIcon from "@mui/icons-material/Info";
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import FileUploadOutlinedIcon from "@mui/icons-material/FileUploadOutlined";
import { setDocuments } from "../../../../store/Slices/documentsSlice";
import { uploadApplicationDocs } from "../../../../services";
import { updateApplication } from "../../../../store/Slices/applicationsSlice";
import { setWordCase } from "../../../../assets/library";
import { setPopup } from "../../../../store/Slices/popupSlice";
import ErrorIcon from '@mui/icons-material/Error';
import { commonStackStyles, commonTypographyStyles } from "../../../../components/styles/customStyles";
// import EastIcon from "@mui/icons-material/East";
import { Recommendation } from "src/types/types";
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';

const CardPage = () => {
  const navigate = useNavigate();
  const shortlisted = useAppSelector((state) => state.shortlistedcourses);
  const applied = useAppSelector((state) => state.applications);
  const recommendations = useAppSelector((state) => state.recommendations);
  const ambitious = recommendations?.data && recommendations?.data?.data?.filter(
    (item: Recommendation) => item?.possibilityOfAdmit === "Ambitious"
  );
  const safe: any = recommendations?.data && recommendations?.data?.data?.filter(
    (item) => item?.possibilityOfAdmit === "Safe"
  );
  const moderate: any = recommendations?.data && recommendations?.data?.data?.filter(
    (item) => item?.possibilityOfAdmit === "Moderate");

  const sections = [
    {
      title: "Activity",
      items: [
        {
          count: `${shortlisted.data?.length}`,
          background: "#FFF7E9",
          link: "/profile/shortlisted",
          label: "Wishlisted Courses", icon: <img src={Images.courseicon} alt="safe" style={{ width: "1.2rem" }} />
        },
        {
          count: `${applied.data?.length}`,
          background: "#FFA89C4D",
          link: "/profile/applications",
          label: "Applied Universities", icon: <img src={Images.courseicon} alt="safe" style={{ width: "1.2rem" }} />
        },
      ],
    },
    {
      title: "Recommendation",
      items: [
        {
          count: `${safe?.length}`,
          link: `/profile/recommendedCourses/Safe`,
          background: "#FFA89C4D",

          label: "Safe", icon: <img src={Images.safe} alt="safe" style={{ width: "1.2rem" }} />
        },
        {
          count: `${moderate?.length}`,
          link: `/profile/recommendedCourses/Moderate`,
          background: "#C9F9F6",

          label: "Moderate", icon: <img src={Images.moderate} alt="safe" style={{ width: "1.2rem" }} />
        },
        {
          count: `${ambitious?.length}`,
          link: `/profile/recommendedCourses/Ambitious`,
          background: "#F4F4FF",

          label: "Ambitious", icon: <ErrorIcon sx={{ fontSize: "1.1rem" }} />
        },
      ],
    },
  ];
  return (
    <div>
      <Box sx={{ borderRadius: "10px", pl: 2, height: { xs: "100%", } }}>
        <React.Fragment >
          <div>
            <Typography sx={{ position: "relative", width: "1.5rem", height: "1.5rem", borderRadius: "50%", background: "#FFF7E9" }}>
              <Typography color="#3b3f76" sx={{ position: "absolute", top: "-4px", left: "14px", width: "200px" }} fontWeight="500" fontSize="1.1rem">
                Activity
              </Typography>
            </Typography>
          </div>
          <Grid container spacing={2} sx={{ mt: -1.5 }}>
            <Grid item xs={12} sx={{ borderRadius: "10px", p: 2 }}>
              <Box sx={{ display: "flex", gap: "1rem" }}>
                {sections?.[0].items.map((item, idx) => (
                  <Stack key={idx} sx={[commonStackStyles, {
                    // border: "1px solid #E07C7C",
                    background: `${item.background}`,
                    p: 2, cursor: "pointer"
                  }]} onClick={() => navigate(item.link)}>
                    <Typography variant="h6" sx={commonTypographyStyles}>
                      {(item.count)}
                    </Typography>
                    <Typography variant="subtitle2" fontWeight="500" sx={commonTypographyStyles}>
                      {typeof item.icon === "string" ? (
                        <>
                          {item.icon}
                        </>
                      ) : (
                        item.icon
                      )}
                      {item.label}
                    </Typography>
                  </Stack>
                ))}
              </Box>

            </Grid>
          </Grid>
        </React.Fragment>
        <React.Fragment >
          <div>
            <Typography sx={{ position: "relative", width: "1.5rem", height: "1.5rem", borderRadius: "50%", background: "#9CFFF9" }}>
              <Typography color="#3b3f76" sx={{ position: "absolute", top: "-4px", left: "14px", width: "200px", mb: 1 }} fontWeight="500" fontSize="1.1rem">
                Recommendations
              </Typography>
            </Typography>
          </div>
          {recommendations.data && recommendations.data?.data?.length > 0 ?
            <>
              <Grid container spacing={2} sx={{ mt: -1.5 }}>
                <Grid item xs={12} sx={{ borderRadius: "10px", p: 2 }}>
                  <Box sx={{ display: "flex", gap: "1rem" }}>
                    <Grid container spacing={2}>
                      {sections?.[1].items.map((item, idx) => (
                        <Grid item xs={6} md={6} lg={4}>
                          <Box key={idx} sx={[{ background: `${item.background}`, p: 2, borderRadius: "10px", cursor: "pointer" }]} onClick={() => navigate(item.link)}>
                            <Typography variant="h6" sx={commonTypographyStyles}>
                              {(item.count)}
                            </Typography>
                            <Typography variant="subtitle2" sx={[commonTypographyStyles,]}>
                              {typeof item.icon === "string" ? (
                                <>
                                  {item.icon}
                                </>
                              ) : (
                                item.icon
                              )}
                              {item.label}
                            </Typography>
                          </Box>
                        </Grid>
                      ))}
                    </Grid>
                  </Box>

                </Grid>
              </Grid>
            </> :
            <Grid container spacing={2} sx={{ background: "#F8F8FC", borderRadius: "10px", mt: 1 }}>
              <Grid item xs={9}>
                <Typography color="#3B3F76" >Oh <span style={{ color: "rgba(48, 51, 99, 0.5)" }}>nooo!</span></Typography>
                <Typography sx={{ mt: 1 }}>Recommendations not generated yet!</Typography>
                <Button sx={{ display: "flex", mt: 1, gap: "1rem", alignItems: "center", cursor: "pointer", color: "#3B3F76", fontWeight: "500", textTransform: "none" }} onClick={() => navigate("/profile/recommendedCourses/generate")}>Go to Recommendations Page </Button>
              </Grid>
              <Grid item xs={3}>
                <img src={Images.noRecommend} alt="" style={{ width: "100px", height: "100px" }} />
              </Grid>
            </Grid>
          }
        </React.Fragment>

      </Box>
    </div>
  );
};

const Checklist = () => {
  const [loading, setLoading] = useState(false);
  const inputFile = useRef<HTMLInputElement>(null);
  const dispatch = useAppDispatch();
  const applications = useAppSelector((state) => state.applications);

  const handleSubmit = async (
    event: React.ChangeEvent<HTMLInputElement>,
    item: any,
    docItem: any
  ) => {
    const file = event.target.files && event.target.files[0];
    try {
      if (file !== null && file !== undefined) {
        const formData = new FormData();
        formData.append("uploaded_file", file || null);
        formData.append("applicationId", item._id);
        formData.append("checklistItemId", docItem._id);
        // setLoading(true)
        const response = await uploadApplicationDocs(formData);
        if (response) {
          dispatch(setDocuments(response.data.data.docs));
          dispatch(
            updateApplication(response.data.data)
          );
          setLoading(false);
        }
      } else {
        setLoading(false);
      }
    } catch (err) {
      setLoading(false);
    }
  };

  const imageClick = () => {
    inputFile?.current?.click();
  };
  const filteredApplications = applications?.data?.filter((item) => {
    return item?.docChecklist?.some((docItem) => !docItem?.isChecked);
  })
    .map((item) => ({
      ...item,
      docChecklist: item?.docChecklist?.filter((docItem) => !docItem?.isChecked),
    }));

  return (
    <Box
      sx={{
        // boxShadow: 1,
      }}
    >
      <div>
        <Typography sx={{ position: "relative", width: "1.5rem", height: "1.5rem", borderRadius: "50%", background: "#FFD8D3" }}>
          <Typography color="#3b3f76" sx={{ position: "absolute", top: "-4px", left: "14px", width: "200px" }} fontWeight="500" fontSize="1.1rem">
            Checklist
          </Typography>
        </Typography>
      </div>
      <Box
        sx={{
          height: "200px",
          background: "#F8F8FC",
          overflowY: "scroll",
          borderRadius: "10px",
          mt: 1
        }}

      >
        {filteredApplications?.length > 0 ? (
          filteredApplications?.map((item) => {
            return item?.docChecklist?.map((docItem) => (
              <>
                <Grid
                  container
                  sx={{
                    px: 2,
                    py: 1,
                    borderRadius: "10px",
                    cursor: "pointer",
                    margin: "10px",
                    width: "auto",
                    background: "transparent",
                    borderBottom: "3px solid #fff"
                  }}
                >
                  <Grid
                    item
                    xs={12}
                    onClick={() =>
                      dispatch(
                        setPopup({
                          show: true,
                          data: {
                            container: {
                              name: "applicationPopup",
                              data: item._id,
                              dimensions: {
                                width: "800px",
                              },
                            },
                            type: "custom",
                          },
                        })
                      )
                    }
                  >
                    <Typography
                      fontSize="14px"
                      display="flex"
                      alignItems="center"
                    >
                      <img
                        src={item?.course?.university?.logoSrc}
                        alt="logo"
                        style={{
                          width: "2rem",
                          height: "2rem",
                          marginRight: "10px",
                          borderRadius: "50%",
                        }}
                      />
                      {item?.course?.name}
                    </Typography>
                  </Grid>
                  <Grid item xs={6}>
                    <Typography fontWeight="500" fontSize="14px">
                      {setWordCase(docItem.name)}
                    </Typography>
                  </Grid>
                  <Grid
                    item
                    xs={6}
                    sx={{
                      display: "flex",
                      height: "20px",
                      justifyContent: "space-between",
                    }}
                  >
                    {docItem?.doc ? (
                      <Box
                        sx={{
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "space-between",
                        }}
                      >
                        {/* <Link
                          to={`/pdfPreview?type=${docItem?.doc?.contentType}&id=${docItem?.doc?._id}`}
                          // to="/pdfPreview"
                          target="_blank"
                          // onClick={() => {
                          //   localStorage.setItem(
                          //     "docId",
                          //     docItem?.doc?._id
                          //   );
                          // }}
                          style={{ textDecoration: "none" }}
                        > */}
                        <VisibilityOutlinedIcon
                          sx={{ fontSize: "large" }}
                          onClick={() => {
                            dispatch(
                              setPopup({
                                show: true,
                                data: {
                                  container: {
                                    name: "fileViewer",
                                    data: docItem?.doc?.data?.preview_url,
                                    dimensions: {
                                      width: "500px",
                                    },
                                  },
                                  type: "custom",
                                }
                              })
                            );
                          }}
                        />
                        <h4
                          style={{
                            color: "#000",
                            display: "flex",
                            alignItems: "center",
                            fontSize: "12px",
                            // width: "120px",
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                            fontWeight:400,marginLeft:"5px"
                          }}
                        >
                          {docItem?.doc?.data?.FileName}
                        </h4>
                        {/* </Link> */}
                      </Box>
                    ) : (
                      <>
                        <input
                          style={{ display: "none" }}
                          type="file"
                          className=""
                          name="image"
                          onChange={(event) =>
                            handleSubmit(event, item, docItem)
                          }
                          ref={inputFile}
                          accept=".pdf"
                        />
                        <Button
                          sx={{
                            color: "#000",
                            textTransform: "none",
                            p: 0.25,
                            fontSize: "0.7rem",
                            ml: 1,
                          }}
                          onClick={imageClick}
                        >
                          {loading ? (
                            <Box
                              component="img"
                              src={Images.standardLoader}
                              alt="standardLoader"
                              sx={{ width: "1rem", height: "1rem" }}
                            />
                          ) : (
                            <FileUploadOutlinedIcon />
                          )}
                        </Button>
                      </>
                    )}
                    <Typography
                      sx={{
                        fontSize: "11px",
                        margin: "2px 2px 0 0",
                        position: "relative",
                        cursor: "pointer",
                        "&:hover .cancel-icon": {
                          display: "block !important",
                        },
                      }}
                    >
                      <InfoOutlinedIcon
                        sx={{ color: "#FF751A", fontSize: "1rem" }}
                      />
                      {docItem?.desc && (
                        <span
                          className="cancel-icon"
                          style={{
                            display: "none",
                            color: "#000",
                            position: "absolute",
                            // border: "1px solid #7d7d7d",
                            padding: "2px",
                            maxWidth: "200px",
                            zIndex: 1,
                            // background: "#fff",
                            textAlign: "center",
                            right: 0,
                            border: "1px solid #000",
                            borderRadius: "5px"
                          }}
                        >
                          {docItem.desc}
                        </span>
                      )}
                    </Typography>
                  </Grid>
                </Grid>
              </>
            ));
          })
        ) : (
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              height: "80%",
            }}
          >
            <img src={Images.noData} alt="nodata" />
            <Typography sx={{ display: "flex" }}>
              There is no checklist
              <Tooltip
                slotProps={{
                  tooltip: {
                    sx: {
                      color: "#514E6A",
                      backgroundColor: "#ffff",
                      boxShadow: 2,
                      cursor: "pointer",
                    },
                  },
                }}
                title="A checklist is a list of items or tasks added by the Student Advisor which needs 
to be completed or verified. It serves as a tool for ensuring that all necessary 
steps or items are addressed in a particular process or activity. Checklists 
are commonly used in various contexts, such as in planning, organizing, quality 
assurance, and everyday tasks, to help individuals or teams stay organized 
and ensure that nothing is overlooked."
                placement="top-start"
              >
                <InfoIcon sx={{ color: "#3B3F76", ml: 1, cursor: "pointer" }} />
              </Tooltip>
            </Typography>
          </Box>
        )}
      </Box>
    </Box>
  );
};

export { CardPage, Checklist };
