import { Avatar, Box, Button, Grid, Tooltip, Typography } from "@mui/material";
import React, { useState } from "react";
import Images from "../../../../assets";
import { useAppDispatch, useAppSelector } from "../../../../assets/hooks";
import { getAdvisorTitle,setWordCase } from "../../../../assets/library";
import { modifyBooking } from "../../../../services";
import { updatemeetings } from "../../../../store/Slices/meetings";
import { setPopup } from "../../../../store/Slices/popupSlice";
import ConfirmDialog from "src/genericComponents/modalPopup/confirmDialog";
import InfoIcon from "@mui/icons-material/Info";

const UpcomingSessions = () => {
  const meetings = useAppSelector((state) => state.meetings);
  const dispatch = useAppDispatch();
  const [open, setOpen] = React.useState(false);
  const [deleteItem,setDeleteItem] = useState();

  const joinNowClick = (link: string) => {
    window.open(link, "_blank");
  };

  const cancelCall = async (id: string) => {
    try {
      const response = await modifyBooking({
        meetingId: deleteItem,
        option: "cancelEvent",
      });
      if (response) {
        dispatch(updatemeetings(response.data));
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "success",
                data: response.data.message,
                dimensions: {
                  width: "30%",
                height:"40%",
                  show:"none"
    
                },
              },
              type: "custom",
            },
          })
        );
      }
    } catch (error) {
      console.error("Error cancelling the event:", error);
    }
    setOpen(false)
  };

  const handleDeleteOpen = (item:any) => {
    setOpen(true);
    setDeleteItem(item);
  };

  const filteredEvents = meetings.data?.filter((event) => {
    const startTime = new Date(event?.data?.start.dateTime);
    const currentTime = new Date();
    return event.status === "upcoming" && startTime > currentTime;
  });

  const rescheduledEvents = meetings.data?.filter((event) => {
    const startTime = new Date(event?.data?.start.dateTime);
    const currentTime = new Date();
    return event.status === "rescheduled" && startTime > currentTime;
  });

  const hasEvents = filteredEvents?.length > 0 || rescheduledEvents?.length > 0;

  return (
    <div>
      <Grid
        container
        spacing={2}
        sx={{
          // height: "40vh",
          mt: 1,
          p: 1,
          overflowY: "scroll",
        }}
      >
        {hasEvents ? (
          <>
            {filteredEvents?.map((data, i) => {
              const startTime = new Date(data?.data?.start.dateTime);
              const endTime = new Date(data?.data?.end.dateTime);
              const currentTime = new Date();
              const isEventInProgress =
                currentTime >= startTime && currentTime <= endTime;
              return (
                <Grid
                  item
                  xs={12}
                  key={i}
                  sx={{
                    display: "flex",
                    position: "relative",
                    background: "#F8F8FC",
                    my: 1,
                    borderRadius: "10px",
                    height:"100px"
                  }}
                >
                  <Grid container spacing={2}>
                    <Grid
                      item
                      xs={4}
                      sx={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                      }}
                    >
                      <Box
                        sx={{
                          position: "absolute",
                          top: "-15px",
                          display: "flex",
                          gap: "1rem",
                          left:0
                        }}
                      >
                        <Avatar
                          src={data?.member.displayPicSrc}
                          alt="logo"
                          style={{
                            width: "2rem",
                            height: "2rem",
                            borderRadius: "50%",
                            marginLeft: "0.8rem",
                          }}
                        />
                        <Typography
                          fontWeight="500"
                          sx={{
                            background: "#fff",
                            p: 1,
                            borderRadius: "10px",
                            fontSize: "14px",
                            color: "#3B3F76",
                          }}
                        >
                          {data?.member?.role
                            ? getAdvisorTitle(data?.member?.role)
                            : getAdvisorTitle(data?.member?.role)}
                        </Typography>
                      </Box>
                      <Typography sx={{mt:-2}}>{setWordCase(data?.member.firstName)}</Typography>
                    </Grid>
                    <Grid item xs={8} sx={{ mt: 1, position: "relative" }}>
                      <Button
                        onClick={() => joinNowClick(data?.data.hangoutLink)}
                        disabled={!isEventInProgress}
                        sx={{
                          border: "1px solid #027A48",
                          textTransform: "none",
                          color: "#000",
                          fontSize: "11px",
                          p: 0,
                          position: "absolute",
                          top: "0",
                          right: "10px",
                        }}
                      >
                        Join now
                      </Button>
                      {/* <Typography sx={{ mt: 0.5 }}>
                        {data?.data?.description ? data?.data?.description : "N/A"}
                      </Typography> */}
                      <Box sx={{display:"flex",mt:2}}>
                      <Typography
                        // color="#C3C3C5"
                        fontSize="14px"
                        gutterBottom
                        sx={{ mb: 1 }}
                      >
                        {new Date(data?.data?.start.dateTime).toLocaleString("en-GB", {
                          month: "long",
                          year: "numeric",
                          day: "numeric",
                          hour: "numeric",
                          minute: "numeric",
                          hour12: true,
                        })}
                      </Typography>
                      <Tooltip
                slotProps={{
                  tooltip: {
                    sx: {
                      color: "#514E6A",
                      backgroundColor: "#ffff",
                      boxShadow: 2,
                      cursor: "pointer",
                    },
                  },
                }}
                title={data?.data?.description ? data?.data?.description : "N/A"}
                placement="top-start"
              >
                <InfoIcon sx={{ color: "#3B3F76", ml: 1, cursor: "pointer",fontSize:"1rem",float:"right" }} />
              </Tooltip>
              </Box>
                      <Typography
                        fontWeight="500"
                        sx={{
                          background: "#fff",
                          p: 0.5,
                          borderRadius: "10px",
                          fontSize: "14px",
                          color: "#3B3F76",
                          width: "fit-content",
                          position: "absolute",
                          bottom: "-15px",
                          right: "10px",
                        }}
                      >
                        <span
                          onClick={() =>
                            dispatch(
                              setPopup({
                                show: true,
                                data: {
                                  container: {
                                    name: "reschedule",
                                    data: data,
                                    dimensions: {
                                      width: "800px",
                                    },
                                  },
                                  type: "custom",
                                },
                              })
                            )
                          }
                          style={{ cursor: "pointer" }}
                        >
                          Reschedule
                        </span>{" "}
                        /
                        <span
                          // onClick={() => cancelCall(data?._id)}
                          onClick={() => handleDeleteOpen(data?._id)}
                          style={{ cursor: "pointer" }}
                        >
                          Cancel
                        </span>
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              );
            })}
            

            {rescheduledEvents?.map((data, i) => {
              const startTime = new Date(data?.data?.start.dateTime);
              const endTime = new Date(data?.data?.end.dateTime);
              const currentTime = new Date();
              const isEventInProgress =
                currentTime >= startTime && currentTime <= endTime;
              return (
                <Grid
                item
                xs={12}
                key={i}
                sx={{
                  display: "flex",
                  position: "relative",
                  background: "#F8F8FC",
                  my: 1,
                  borderRadius: "10px",
                  height:"100px"
                }}
              >
                <Grid container spacing={2}>
                  <Grid
                    item
                    xs={4}
                    sx={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <Box
                      sx={{
                        position: "absolute",
                        top: "-15px",
                        display: "flex",
                        gap: "1rem",
                        left:0
                      }}
                    >
                      <Avatar
                        src={data?.member.displayPicSrc}
                        alt="logo"
                        style={{
                          width: "2rem",
                          height: "2rem",
                          borderRadius: "50%",
                          marginLeft: "0.8rem",
                        }}
                      />
                      <Typography
                        fontWeight="500"
                        sx={{
                          background: "#fff",
                          p: 1,
                          borderRadius: "10px",
                          fontSize: "14px",
                          color: "#3B3F76",
                        }}
                      >
                        {data?.member?.role
                          ? getAdvisorTitle(data?.member?.role)
                          : getAdvisorTitle(data?.member?.role)}
                      </Typography>
                    </Box>
                    <Typography sx={{mt:-2}}>{setWordCase(data?.member.firstName)}</Typography>
                  </Grid>
                  <Grid item xs={8} sx={{ mt: 1, position: "relative" }}>
                    <Button
                      onClick={() => joinNowClick(data?.data.hangoutLink)}
                      disabled={!isEventInProgress}
                      sx={{
                        border: "1px solid #027A48",
                        textTransform: "none",
                        color: "#000",
                        fontSize: "11px",
                        p: 0,
                        position: "absolute",
                        top: "0",
                        right: "10px",
                      }}
                    >
                      Join now
                    </Button>
                    {/* <Typography sx={{ mt: 0.5 }}>
                      {data?.data?.description ? data?.data?.description : "N/A"}
                    </Typography> */}
                    <Box sx={{display:"flex",mt:2}}>
                    <Typography
                      // color="#C3C3C5"
                      fontSize="14px"
                      gutterBottom
                      sx={{ mb: 1 }}
                    >
                      {new Date(data?.data?.start.dateTime).toLocaleString("en-GB", {
                        month: "long",
                        year: "numeric",
                        day: "numeric",
                        hour: "numeric",
                        minute: "numeric",
                        hour12: true,
                      })}
                    </Typography>
                    <Tooltip
              slotProps={{
                tooltip: {
                  sx: {
                    color: "#514E6A",
                    backgroundColor: "#ffff",
                    boxShadow: 2,
                    cursor: "pointer",
                  },
                },
              }}
              title={data?.data?.description ? data?.data?.description : "N/A"}
              placement="top-start"
            >
              <InfoIcon sx={{ color: "#3B3F76", ml: 1, cursor: "pointer",fontSize:"1rem",float:"right" }} />
            </Tooltip>
            </Box>
                    <Typography
                      fontWeight="500"
                      sx={{
                        background: "#fff",
                        p: 0.5,
                        borderRadius: "10px",
                        fontSize: "14px",
                        color: "#3B3F76",
                        width: "fit-content",
                        position: "absolute",
                        bottom: "-15px",
                        right: "10px",
                      }}
                    >
                      <span
                        onClick={() =>
                          dispatch(
                            setPopup({
                              show: true,
                              data: {
                                container: {
                                  name: "reschedule",
                                  data: data,
                                  dimensions: {
                                    width: "800px",
                                  },
                                },
                                type: "custom",
                              },
                            })
                          )
                        }
                        style={{ cursor: "pointer" }}
                      >
                        Reschedule
                      </span>{" "}
                      /
                      <span
                        // onClick={() => cancelCall(data?._id)}
                        onClick={() => handleDeleteOpen(data?._id)}
                        style={{ cursor: "pointer" }}
                      >
                        Cancel
                      </span>
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
              );
            })}
          </>
        ) : (
          <Grid
            item
            xs={12}
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              flexDirection: "column",
            }}
          >
            <Typography color="#3B3F76" fontSize="1.2rem" fontWeight="500">
              No <span style={{ color: "rgba(48, 51, 99, 0.5)" }}>upcoming</span> meetings
            </Typography>
            <img
              src={Images.noMeetings}
              alt="noData"
              style={{ width: "250px", marginTop: "1rem" }}
            />
          </Grid>
        )}
      </Grid>
      <ConfirmDialog
        open={open}
        handleClose={() => setOpen(false)}
        additionalData={{
          onSubmit:cancelCall, 
          title: "Are you sure you want to cancel this meeting?",
          content:""
        }}
      />
    </div>
  );
};

export default UpcomingSessions;
