import {
  // Box,
  FormControl,
  FormControlLabel,
  Grid,
  InputAdornment,
  InputLabel,
  ListSubheader,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  TextField,
  Tooltip,
} from "@mui/material";
import { ErrorMessage, Form, Formik } from "formik";
import React, { useEffect, useMemo, useState } from "react";
import CustomField from "../../../../genericComponents/customTextfield";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { editProfile } from "../../../../services";
import { setPlus2 } from "../../../../store/Slices/educationHistorySlice";
import { useAppDispatch } from "../../../../assets/hooks";
import { boards, languages} from "../../../../assets/menu";
import { setPopup } from "../../../../store/Slices/popupSlice";
import { Country, State, City } from "country-state-city";
import { Event } from "../../../../types/types";
import { CustomButton } from "../../../../genericComponents/customButton";
import * as Yup from 'yup';
import InfoIcon from "@mui/icons-material/Info";
import ReusableDropdown from "src/genericComponents/reusableSelect";
import SearchIcon from "@mui/icons-material/Search";
import DatePickerComponent from "src/genericComponents/datePicker";


interface ICountry {
  name: string;
  isoCode: string;
}

interface IState {
  name: string;
  isoCode: string;
  countryCode: string;
}

interface ICity {
  name: string;
  stateCode: string;
  countryCode: string;
}
const Plus2 = ({data}:any) => {
  const dispatch = useAppDispatch();
  const [Isbacklog,setIsbacklog] = useState()
  const [countries, setCountries] = useState<ICountry[]>([]);
  const [states, setStates] = useState<IState[]>([]);
  const [cities, setCities] = useState<ICity[]>([]);
  const [selectedCountry, setSelectedCountry] = useState(
    data?.country || "India"
  );
  const [selectedState, setSelectedState] = useState(
    data?.state || ""
  );
  const [selectedCity, setSelectedCity] = useState(
    data?.city || ""
  );
  const countryInfo = countries.find((c: any) => c.name === selectedCountry);
  const stateInfo = states.find((s: any) => s.name === selectedState);
  const [gradingSystem, setGradingSystem] = useState(data?.gradingSystem);
  const [totalScore, setTotalScore] = useState(data?.totalScore);
  const [stream, setStream] = useState<string>(data?.stream || "");
  const [customField, setCustomField] = useState<boolean>(false);
  // const [showStream, setShowStream] = useState<boolean>(false);
  const [board, setBoard] = useState<string>(data?.board || "");
  // const [showBoard, setShowBoard] = useState<boolean>(false);
 
  const handleGradingSystemChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setGradingSystem(event.target.value as string);
    setTotalScore('');
  };
  useEffect(() => {
    const fetchCountries = async () => {
      const allCountries = await Country.getAllCountries();
      setCountries(allCountries);
    };

    fetchCountries();
  }, []);

  useEffect(() => {
    const fetchStateAndCities = async () => {
      const statesOfCountry = await State.getStatesOfCountry(
        countryInfo?.isoCode
      );
      setStates(statesOfCountry);

      const citiesOfState = await City.getCitiesOfState(
        stateInfo?.countryCode ?? "",
        stateInfo?.isoCode ?? ""
      );
      setCities(citiesOfState);
    };
    fetchStateAndCities();
  }, [countryInfo, stateInfo]);
  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 150,
      },
    },
  };


  const initialValues = {
    plus2: {
      instituteName:data?.instituteName,
      city: selectedCity,
      state: selectedState,
      country: selectedCountry,
      languageOfInstruction:data?.languageOfInstruction,
      gradingSystem:data?.gradingSystem, // enum % grade gpa cgpa
      board:board,
      stream:stream,
      totalScore:data?.totalScore, // for grade A+..., for Percent 0-100, gpa 0-10
      startDate:data?.startDate,
      endDate:data?.endDate,
      backlogs:data?.backlogs,
      isCompleted:data?.isCompleted,
      custom: customField
    },
  };
  const eventHandler = async (event: Event) => {
    switch (event.name) {
      case "Country":
        setSelectedCountry(event.data);
        setSelectedState('');
        setSelectedCity('');
        setCities([]);

        const countryInfo = countries.find((c: any) => c.name === event.data);
        if (countryInfo) {
          const statesOfCountry = await State.getStatesOfCountry(countryInfo.isoCode);
          setStates(statesOfCountry);
        }
        break;

      case "State":
        setSelectedCity('');
        setSelectedState(event.data);
        const stateInfo = states.find((s: any) => s.name === event.data);
        if (stateInfo) {
          const citiesOfState = await City.getCitiesOfState(stateInfo.countryCode, stateInfo.isoCode);
          setCities(citiesOfState);
        }
        break;

      case "City":
        setSelectedCity(event.data);
        break;
      default:
        break;

    }
  };
  const [searchQuery, setSearchQuery] = useState("");

  const filteredCities = useMemo(
    () =>
      cities.filter((city) =>
        city.name.toLowerCase().includes(searchQuery.toLowerCase())
      ),
    [cities, searchQuery]
  );
  const cityHandle = (newCity: string) => {
    setSelectedCity(newCity);
  };
  const submit = async (values: any) => {
    try {
    const response = await editProfile(values);
    // console.log(values)
  if (response) {
    dispatch(setPlus2(response.data.data.education.plus2));
    dispatch(
      setPopup({
        show: true,
        data: {
          container: {
            name: "success",
            data: "Diploma details added successfully ",
            dimensions: {
              width: "35%",
              height:"40%",
              show:"none"

            },
          },
          type: "custom",
        },
      })
    );
  }
} catch(error){}
  };
// eslint-disable-next-line
  const validationSchema = Yup.object().shape({
    plus2: Yup.object().shape({
      instituteName: Yup.string().required("Institution name is required"),
      // city: Yup.string().required("City is required"),
      state: Yup.string().required("State is required"),
      country: Yup.string().required("Country is required"),
      // languageOfInstruction: Yup.string().required(
      //   "Language of instruction is required"
      // ),
      gradingSystem: Yup.string().required("Grading system is required"),
      totalScore: Yup.mixed() 
      .required('Score is required')
      .test('valid-score', 'Invalid score', function (value) {
        const { gradingSystem } = this.parent;
        if (gradingSystem === 'percentage') {
          return typeof value === 'number' && value >= 1 && value <= 100;
        }
        if (gradingSystem === 'GPA') {
          return typeof value === 'number' && value >= 1 && value <= 10;
        }
        if (gradingSystem === 'CGPA') {
          return typeof value === 'number' && value >= 1 && value <= 6;
        }

        if (gradingSystem === 'grade') {
          return typeof value === 'string' && ['A', 'A+', 'B', 'B+', 'C', 'D'].includes(value);
        }

        return true; 
      })
    ,
      startDate: Yup.date().required("Start date is required"),
      endDate: Yup.date()
        .when("isCompleted", {
          is: true,
          then: schema => schema.required("End Date is required"),
        })
        .nullable(),
      // backlogs: Yup.number()
      //   .when("isCompleted", {
      //     is: "false",
      //     then: Yup.number().notRequired(),
      //     otherwise: Yup.number().required("Backlog count is required"),
      //   })
      //   .nullable(),
    }),
  });
  return (
    <div>
      <Formik
        initialValues={initialValues}
        // validationSchema={validationSchema}
        enableReinitialize={true}
        onSubmit={(values: any) => submit(values)}
      >
        {({ touched, errors, values, handleChange, resetForm }) => {
          return (
            <Form style={{}}>
              <Grid container spacing={3} sx={{px:2}}>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Specialization/Subjects
                  </InputLabel>
                  <TextField
                    value={stream}
                    onChange={(e) => setStream(e.target.value)}
                    placeholder="Enter your stream name"
                    variant="outlined"
                     name="plus2.stream"
                    size="small"
                    autoComplete="off"
                    fullWidth
                    // sx={{ mt: 2 }}
                  />

                  <ErrorMessage
                      name="plus2.stream"
                      component="div"
                      className="error"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel id="" sx={{ color: "#000", fontWeight: 500 }}>
                    Education status
                  </InputLabel>
                  <FormControl>
                    <RadioGroup
                      row
                      aria-labelledby="demo-row-radio-buttons-group-label"
                      name="plus2.isCompleted"
                      value={values.plus2.isCompleted}
                      onChange={handleChange}
                    >
                      <FormControlLabel
                        value="true"
                        control={
                          <Radio
                            size="small"
                            sx={{
                              "&.MuiRadio-root.Mui-checked": {
                                color: "#3B3F76 !important",
                              },
                            }}
                          />
                        }
                        label="Completed"
                      />
                      <FormControlLabel
                        value="false"
                        control={
                          <Radio
                            size="small"
                            sx={{
                              "&.MuiRadio-root.Mui-checked": {
                                color: "#3B3F76 !important",
                              },
                            }}
                          />
                        }
                        label="Pursuing"
                      />
                    </RadioGroup>
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Name of Institution{" "}
                  </InputLabel>
                  <CustomField
                    id="name"
                    type="text"
                    placeholder="Name of Institution"
                    name="plus2.instituteName"
                    value={values.plus2.instituteName}
                    onChange={handleChange}
                    fullWidth
                  />
                  <ErrorMessage
                      name="plus2.instituteName"
                      component="div"
                      className="error"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                <InputLabel id="" sx={{ color: "#000",display:"flex" ,alignItems:"center"}}>
                    Board
                    <Tooltip
                slotProps={{
                  tooltip: {
                    sx: {
                      color: "#514E6A",
                      backgroundColor: "#ffff",
                      boxShadow: 2,
                      cursor: "pointer",
                    },
                  },
                }}
                title={"A board in education is a governing body that manages an educational system or part of it"}
                placement="right"
              >
                <InfoIcon sx={{ color: "#3B3F76", ml: 1, cursor: "pointer",fontSize:"1rem",float:"right" }} />
              </Tooltip>
                  </InputLabel>

                  <Select
                    size="small"
                    id="Country"
                    name="plus2.board"
                    displayEmpty
                    defaultValue=""
                    value={board}
                    onChange={(e)=>setBoard(e.target.value)}
                    fullWidth
                    IconComponent={ExpandMoreIcon}
                    MenuProps={MenuProps}
                  >
                    <MenuItem
                      disabled
                      value=""
                      sx={{
                        "& .MuiTypography-root": {
                          fontSize: "14px !important",
                        },
                      }}
                    >
                      Select
                    </MenuItem>

                    {boards.map(
                      (name: any) => (
                        <MenuItem
                          key={name}
                          value={name}
                          sx={{
                            "& .MuiTypography-root": {
                              fontSize: "14px !important",
                            },
                            "& .MuiSvgIcon-root ": {
                              fontSize: "1rem",
                            },
                            whiteSpace: "normal",
                          }}
                        >
                          {name}
                        </MenuItem>
                      )
                    )}
                     {!boards.includes(board) && board !== "" && (
        <MenuItem key={board} value={board}>
          {board}
        </MenuItem>
      )}
        <MenuItem
                        sx={{
                          fontSize: "14px !important",
                          display: "flex",
                          alignItems: "center",
                          whiteSpace: "normal",
                          color:"#1cbeb5  !important"
                        }}
                        onClick={() => {
                          setCustomField(true)
                          // setShowBoard(true)
                          dispatch(
                            setPopup({
                              show: true,
                              data: {
                                container: {
                                  name: "customfield",
                                  data: {
                                    name:"plus2.board",
                                    fieldName:board,
                                    setFieldName:setBoard,
                                    placeholder:"Enter your board"
                                  },
                                  dimensions: {
                                    width: "450px",
                                  },
                                },
                                type: "custom",
                              },
                            })
                          )
                        }}
                      >
                        Can't find your board
                      </MenuItem>
                  </Select>

                  <ErrorMessage
                      name="plus2.board"
                      component="div"
                      className="error"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Grading System
                  </InputLabel>

                  <Select
                    id="Country"
                    size="small"
                    name="plus2.gradingSystem"
                    displayEmpty
                    defaultValue=""
                    value={gradingSystem}
                    onChange={(e: any) => handleGradingSystemChange(e)}
                    fullWidth
                    IconComponent={ExpandMoreIcon}
                  >
                    <MenuItem value="" disabled>Select grade</MenuItem>
                    <MenuItem value="percentage">Percentage</MenuItem>
                    <MenuItem value="gpa">CGPA    4.0 scale</MenuItem>
                    <MenuItem value="cgpa">CGPA   10.0 scale</MenuItem>
                    <MenuItem value="grade">Grade</MenuItem>
                  </Select>
                  <ErrorMessage
                      name="plus2.gradingSystem"
                      component="div"
                      className="error"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Score{" "}
                  </InputLabel>
                  {gradingSystem === "grade" ?
                    <Select
                      size="small"
                      id="totalScore"
                      name="plus2.totalScore"
                      displayEmpty
                      value={totalScore}
                      onChange={(e) => setTotalScore(e.target.value)}
                      fullWidth
                      IconComponent={ExpandMoreIcon}
                      MenuProps={MenuProps}
                    >
                      <MenuItem disabled value="">
                        Select Grade
                      </MenuItem>
                      {["A", "A+", "B", "B+", "C", "C+", "D", "D+"].map((name: string) => (
                        <MenuItem key={name} value={name}>
                          {name}
                        </MenuItem>
                      ))}
                    </Select>
                    :
                    <TextField
                      // type="number"
                      name="plus2.totalScore"
                      size="small"
                      fullWidth
                      value={totalScore}
                      onChange={(e) => setTotalScore(e.target.value)}
                      placeholder={
                        gradingSystem === "percentage"
                          ? "Enter percentage"
                          : gradingSystem === "grade"
                            ? "Enter grade"
                            : "Enter score"
                      }
                      inputProps={{
                        step: "0.1", 

                        min: gradingSystem === "percentage" ? 1 : gradingSystem === "gpa" ? 1 : gradingSystem === "cgpa" ? 1 : undefined,
                        max: gradingSystem === "percentage" ? 100 : gradingSystem === "gpa" ? 4 : gradingSystem === "cgpa" ? 10 : undefined,
                        list: gradingSystem === "grade" ? "grades" : undefined,
                        onInput: (e) => {
                          const target = e.target as HTMLInputElement;
                          const value = parseFloat(target.value);
                          if (gradingSystem !== "Grade") {
                            const min = gradingSystem === "percentage" ? 1 : gradingSystem === "gpa" ? 1 : gradingSystem === "cgpa" ? 1 : undefined;
                            const max = gradingSystem === "percentage" ? 100 : gradingSystem === "gpa" ? 4 : gradingSystem === "cgpa" ? 10 : undefined;

                            if (value < (min || 0) || value > (max || Infinity)) {
                              target.value = value.toString().slice(0, -1); // Remove the last character
                            }
                          }
                        },
                      }}
                    />}

                  <ErrorMessage
                    name="plus2.totalScore"
                    component="div"
                    className="error"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Start Date
                  </InputLabel>
                  <DatePickerComponent
                    selectedDate={values?.plus2?.startDate}
                    onChange={(date) => handleChange({ target: { name: `plus2.startDate`, value: date?.toISOString().slice(0, 10) } })}
                    maxDate={new Date()}
                    placeholderText="Start Date"
                  />
                  <ErrorMessage
                      name="plus2.startDate"
                      component="div"
                      className="error"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                  {values.plus2.isCompleted === true ?(
                    <>
                      <InputLabel
                        id="instituteName"
                        sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                      >
                        End Date
                      </InputLabel>
                      <DatePickerComponent
                        selectedDate={values?.plus2?.endDate}
                        onChange={(date) =>
                          handleChange({
                            target: {
                              name: `plus2.endDate`,
                              value: date?.toISOString().slice(0, 10),
                            },
                          })
                        }
                        minDate={
                          values.plus2.startDate
                            ? new Date(
                              new Date(values.plus2.startDate).setFullYear(
                                new Date(values.plus2.startDate).getFullYear() + 1
                              )
                            )
                            : undefined
                        }
                        maxDate={
                          values.plus2.startDate
                            ? (() => {
                                const fourYearsFromStartDate = new Date(
                                  new Date(values.plus2.startDate).setFullYear(
                                    new Date(values.plus2.startDate).getFullYear() + 2
                                  )
                                );
                                return fourYearsFromStartDate > new Date() ? new Date() : fourYearsFromStartDate;
                              })()
                            : new Date()
                        }
                        placeholderText="End Date"
                      />
                    </>
                  ): null}
                  <ErrorMessage
                      name="plus2.endDate"
                      component="div"
                      className="error"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Language of instruction
                  </InputLabel>
                  <ReusableDropdown
                    label="Language of Instruction"
                    name="plus2.languageOfInstruction"
                    options={languages}
                    value={values.plus2.languageOfInstruction}
                    onChange={handleChange}
                    placeholder="Select"
                    searchEnabled={true}  // Enables the search box
                  />
                  
                </Grid>
                <Grid item xs={12} sm={6}>
                <InputLabel id="" sx={{ color: "#000",display:"flex" ,alignItems:"center"}}>
                    Backlogs
                    <Tooltip
                slotProps={{
                  tooltip: {
                    sx: {
                      color: "#514E6A",
                      backgroundColor: "#ffff",
                      boxShadow: 2,
                      cursor: "pointer",
                    },
                  },
                }}
                title={"Backlog refers to a failed subject or exam that you have not cleared in your first attempt"}
                placement="right"
              >
                <InfoIcon sx={{ color: "#3B3F76", ml: 1, cursor: "pointer",fontSize:"1rem",float:"right" }} />
              </Tooltip>
                  </InputLabel>
                <FormControl>
                  <RadioGroup
                    row
                    aria-labelledby="demo-row-radio-buttons-group-label"
                    // name="plus2.backlogs"
                    value={values.plus2.backlogs? "yes":"no"}
                    onChange={(e:any) =>setIsbacklog(e.target.value)}
                  >
                    <FormControlLabel
                      value="yes"
                      control={
                        <Radio
                          size="small"
                          sx={{
                            "&.MuiRadio-root.Mui-checked": {
                              color: "#3B3F76 !important",
                            },
                          }}
                        />
                      }
                      label="Yes"
                    />
                    <FormControlLabel
                      value="no"
                      control={
                        <Radio
                          size="small"
                          sx={{
                            "&.MuiRadio-root.Mui-checked": {
                              color: "#3B3F76 !important",
                            },
                          }}
                        />
                      }
                      label="No"
                    />
                  </RadioGroup>
                </FormControl>
              </Grid>
              {Isbacklog === "yes" ? <Grid item xs={12} sm={6}>
               <InputLabel id="" sx={{ color: "#000" }}>
                Number of Backlogs
               </InputLabel>
               <CustomField
                 id="name"
                 type="number"
                 placeholder="Number of backlogs"
                 name="plus2.backlogs"
                 value={values.plus2.backlogs}
                 onChange={handleChange}
                 fullWidth
               />
             </Grid> : null}

             <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    Country
                  </InputLabel>
                  <ReusableDropdown
                    label="Country"
                    name="plus2.country"
                    options={countries.map(country => country.name)}
                    value={selectedCountry}
                    onChange={(e: any) => eventHandler({
                      name: "Country",
                      data: e?.target.value,
                    })}
                    placeholder="Select"
                    searchEnabled={true}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    State/Province
                  </InputLabel>
                  <ReusableDropdown
                    label="State"
                    name="plus2.state"
                    options={states.map(state => state.name)}
                    value={selectedState}
                    onChange={(e: any) => eventHandler({
                      name: "State",
                      data: e?.target.value,
                    })}
                    placeholder="Select"
                    searchEnabled={true}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <InputLabel
                    id="instituteName"
                    sx={{ fontWeight: "500", color: "#000", mb: 1 }}
                  >
                    City/Town
                  </InputLabel>
                  <FormControl fullWidth>
                    <Select
                      id="city"
                      name="plus2.city"
                      value={selectedCity}
                      displayEmpty
                      size="small"
                      defaultValue=""
                      onChange={(e) => setSelectedCity(e.target.value)}
                      fullWidth
                      IconComponent={ExpandMoreIcon}
                      onClose={() => setSearchQuery("")} // Reset search when dropdown closes
                      MenuProps={{
                        PaperProps: {
                          sx: {
                            maxHeight: 300,
                            "&.MuiPaper-root": {
                              boxShadow: 3,
                              borderRadius: "15px !important",
                            },
                          },
                        },
                      }}
                    >
                      <ListSubheader>
                        <TextField
                          size="small"
                          autoFocus
                          placeholder="Search City..."
                          fullWidth
                          autoComplete="off"
                          // autoFocus="true"
                          value={searchQuery}
                          onChange={(e) => setSearchQuery(e.target.value)}
                          InputProps={{
                            startAdornment: (
                              <InputAdornment position="start">
                                <SearchIcon />
                              </InputAdornment>
                            ),
                          }}
                          onKeyDown={(e) => {
                            if (e.key !== "Escape") {
                              e.stopPropagation();
                            }
                          }}
                        />
                      </ListSubheader>

                      <MenuItem value="" disabled>
                        {!selectedState ? "Please select state" :
                          "Select city"}
                      </MenuItem>

                      {filteredCities.length > 0 ? (
                        filteredCities.map((city: any) => (
                          <MenuItem key={city.name} value={city.name}>
                            {city.name}
                          </MenuItem>
                        ))
                      ) : (
                        selectedState && <MenuItem disabled>No cities available</MenuItem>
                      )}

                      {!cities.some((city) => city.name === selectedCity) &&
                        selectedCity !== undefined &&
                        selectedCity !== "" && (
                          <MenuItem key={selectedCity} value={selectedCity}>
                            {selectedCity}
                          </MenuItem>
                        )}

                      <MenuItem
                        sx={{
                          fontSize: "14px !important",
                          display: "flex",
                          alignItems: "center",
                          whiteSpace: "normal",
                          color: "#1cbeb5 !important",
                        }}
                        // value=""
                        onClick={() => {
                          // setSelectedCity("")
                          dispatch(
                            setPopup({
                              show: true,
                              data: {
                                container: {
                                  name: "customfield",
                                  data: {
                                    name: "plus2.city",
                                    fieldName: selectedCity,
                                    setFieldName: cityHandle,
                                    placeholder: "Enter your city",
                                  },
                                  dimensions: {
                                    width: "450px",
                                  },
                                },
                                type: "custom",
                                // eventHandler:cityHandle
                              },
                            })
                          );
                        }}
                      >
                        Can't find city
                      </MenuItem>
                    </Select>
                  </FormControl>

                </Grid>

                <Grid
                  item
                  xs={12}
                  sx={{ display: "flex", justifyContent: "end", my: 2,mr:10 }}
                >
                  <CustomButton
                    handleSubmit={() => submit}
                      width= "100px"
                  >
                    Save
                  </CustomButton>
                </Grid>
              </Grid>
            </Form>
          );
        }}
      </Formik>
    </div>
  );
};

export default Plus2;
