import { Box, Divider, Grid, Stack, Tooltip, Typography } from '@mui/material'
import React from 'react'
import Images from 'src/assets'
import { useAppSelector } from 'src/assets/hooks'
import TimerIcon from "@mui/icons-material/Timer";
import CustomCard from 'src/genericComponents/customCard'
import Razor from './checkout/razor';
import { setWordCase } from 'src/assets/library';

const Orders = () => {
  const orders = useAppSelector(store => store.orders)
  // console.log(orders)
  function formatDate(date: Date): string {
    const day = String(date.getDate()).padStart(2, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are 0-based
    const year = date.getFullYear();

    return `${day}-${month}-${year}`;
  }
  function date(date: Date) {
    const month = date.toLocaleString("default", { month: "short" });
    const year = date.getFullYear();
    return month + " " + year;
  }
  return (
    <div style={{
      // height:"100vh",
      // overflowY: "scroll",
      padding: "2rem"
    }}>
      <Typography sx={{ position: "relative", width: "1.5rem", height: "1.5rem", borderRadius: "50%", background: "#75ECE5", mb: 2 }}>
        <Typography color="#3b3f76" sx={{ position: "absolute", top: "-4px", left: "14px", width: "200px" }} fontWeight="500" fontSize="1.1rem">
          Orders
        </Typography>
      </Typography>
      <Grid container spacing={3} sx={{}}>
        {orders?.data?.length > 0 ? (
          orders?.data?.map((item, i) => (
            <React.Fragment key={i}>
              <Grid item xs={12}>
                <Box sx={{}}>
                  <Grid container spacing={2}>
                    {item?.products?.map((product, j) => (
                      <Grid item xs={12} md={6} lg={4} key={j}>
                        <CustomCard title={product.course?.university?.location?.country} i={j}>
                          <Box>
                            {product?.cancellationRequest === true ? (
                              <img
                                src={Images.cancelledIcon}
                                alt="cancelled"
                                style={{
                                  position: "absolute",
                                  top: "-28px",
                                  right: "-26px",
                                }}
                              />
                            ) : (
                              ""
                            )}
                            <Box sx={{ display: "flex", gap: "10px", width: "100%" }}>
                              <img
                                src={product.course?.university?.logoSrc || Images.establishedYear}
                                alt="logo"
                                style={{
                                  width: "1.5rem",
                                  height: "1.5rem",
                                  marginRight: "5px",
                                  borderRadius: "10px",
                                }}
                              />
                              <div style={{ flex: 1 }}>
                                <Tooltip
                                  slotProps={{
                                    tooltip: {
                                      sx: {
                                        color: "#fff",
                                        backgroundColor: "#3B3F76",
                                        boxShadow: 1,
                                        "&.MuiTooltip-tooltip": {
                                          marginTop: "-5px !important",
                                        },
                                      },
                                    },
                                  }}
                                  title={product?.course?.name}
                                  placement="bottom"
                                >
                                  <Typography
                                    fontSize="0.9rem"
                                    sx={{
                                      fontSize: { xs: "13px", lg: "14px" },
                                      fontWeight: 500,
                                      textOverflow: "ellipsis ",
                                      lineClamp: 1,
                                      height: "1.5rem",
                                      textTransform: "capitalize",
                                      overflow: "hidden",
                                      display: "-webkit-box",
                                      WebkitLineClamp: "1",
                                      WebkitBoxOrient: "vertical",
                                    }}
                                  >
                                    {product?.course?.name}
                                  </Typography>
                                </Tooltip>
                                <Typography
                                  fontSize="0.9rem"
                                  sx={{
                                    fontSize: { xs: "12px", lg: "12px" },
                                    height: "1.5rem",
                                    textTransform: "capitalize",
                                    display: "-webkit-box",
                                    WebkitBoxOrient: "vertical",
                                    my: 1,
                                  }}
                                >
                                  {product.course?.university?.name}

                                </Typography>
                              </div>
                            </Box>
                            <Stack
                              direction="row"
                              justifyContent="end"
                              spacing={1}
                              sx={{ mt: 1 }}
                            >
                              <Typography
                                component="span"
                                fontSize="0.8rem"
                                fontWeight="500"
                                sx={{
                                  display: "flex",
                                  alignItems: "center",
                                  whiteSpace: "nowrap",
                                }}
                              >
                                <TimerIcon sx={{ fontSize: "1.25rem" }} />
                                &nbsp; Start date :
                                {product?.intake ? date(new Date(product?.intake)) : null}
                              </Typography>
                            </Stack>
                            {/* <p
                        style={{
                          position: "absolute",
                          left: "10px",
                          bottom: "10%",
                          background: "#B7B7E7",
                          width: "1.1rem",
                          height: "1.1rem",
                          margin: 0,
                          borderBottomLeftRadius: "20px",
                          transform: "rotate(45deg)",
                        }}
                      ></p> */}
                          </Box>
                        </CustomCard>
                      </Grid>
                    ))}
                  </Grid>
                  <Box sx={{ mt: 2, display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                    <Box>
                      {item?.paymentDetails?.paymentStatus !== "paid" ? <Razor data={item?._id} total={Math.floor(item?.paymentDetails?.amount / 100).toLocaleString("en-US")} type="reorder" /> : ""}
                    </Box>
                    <Box sx={{ float: 'right' }}>
                      <Typography fontSize="0.9rem" color="#8D8B8B">Order total:{item?.paymentDetails?.currency} {Math.floor(item?.paymentDetails?.amount / 100).toLocaleString("en-US")}</Typography>
                      <Typography fontSize="0.9rem" color="#8D8B8B" sx={{ color: item?.paymentDetails?.paymentStatus === "paid" ? "green" : "red" }}>Status:{setWordCase(item?.paymentDetails?.paymentStatus)}</Typography>
                      <Typography fontSize="0.9rem" color="#8D8B8B">Order placed: {item?.paymentDetails?.created_at ? formatDate(new Date(item?.paymentDetails?.created_at)) : "N/A"}</Typography>
                    </Box>
                  </Box>
                </Box>
              </Grid>
              {i < orders.data.length - 1 && (
                <Grid item xs={12}>
                  <Divider />
                </Grid>
              )}
            </React.Fragment>
          ))
        ) : (
          <Grid
            item
            xs={12}
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "50vh",
              flexDirection: "column",
            }}
          >
            <img src={Images.noData} alt="noData" style={{ width: "70px", height: "70px" }} />
            <Typography>No orders </Typography>
          </Grid>
        )}
      </Grid>
    </div>
  )
}

export default Orders